<template>
  <div class="wrapper">
    <!--轮播图-->
    <div class="swiper-container wrap">
      <div class="swiper-wrapper">
        <div class="swiper-slide"><img src="../../assets/img/1首页/1推荐/lunbodazao.png" alt="" @click="tolunbo"></div>
        <div class="swiper-slide"><img src="../../assets/img/1首页/1推荐/lunbo_xunli.png" alt=""></div>
        <div class="swiper-slide"><img src="../../assets/img/1首页/1推荐/lunbodazao.png" alt=""></div>
      </div>
      <!-- 如果需要分页器 -->
      <div class="swiper-pagination"></div>
      <!-- 如果需要滚动条 -->
      <div class="swiper-scrollbar"></div>
    </div>
    <!--促销模块-->
    <div class="cuxiao">
      <div class="title" @click="toNewGoods">
        <img src="../../assets/img/1首页/1推荐/laba.png" alt="">
        <span>新品上市</span>
        <span>促销大庆</span>
      </div>
      <div class="content">
        <div class="left" @click="toOutTimeGoods">
          <img src="../../assets/img/1首页/1推荐/xianshiduanhuo.png" alt="" class="contimg">
        </div>
        <div class="right">
          <div @click="toBrands">
            <img src="../../assets/img/1首页/1推荐/jingpin.png" alt="" class="rigtop">
          </div>
          <div @click="toHotSale">
            <img src="../../assets/img/1首页/1推荐/chaozhiremai.png" alt="" class="rigbottom">
          </div>
        </div>
      </div>
    </div>
    <!--今日推荐-->
    <div class="tuijian">
      <div class="title">
        <img src="../../assets/img/1首页/1推荐/rili.png" alt="">
        <span>今日推荐</span>
      </div>
      <div @click="toGorgeous">
        <img src="../../assets/img/1首页/1推荐/lunbo_xunli.png" alt="" class="bg">
      </div>
      <div class="three">
        <ul class="flex">
          <li><img src="../../assets/img/1首页/1推荐/hualeichuncai.png" alt=""></li>
          <li style="border-right: 1px solid gainsboro;
    border-left: 1px solid gainsboro;"><img src="../../assets/img/1首页/1推荐/jiaochuankouhong.png" alt=""></li>
          <li><img src="../../assets/img/1首页/1推荐/kouhongjinse.png" alt=""></li>
        </ul>
      </div>
      <div class="three">
        <ul class="flex" style="color: red">
          <li>
            <div>不易脱妆口红</div>
            <div>¥59</div>
          </li>
          <li>
            <div>花心唇彩</div>
            <div>¥129</div>
          </li>
          <li>
            <div>娇吻玉口红</div>
            <div>¥88</div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import * as $ from 'jquery'
  import * as Swiper from 'swiper'
  import "../../../node_modules/swiper/dist/css/swiper.css"

  export default {
    methods:{
      toNewGoods() {
        this.$router.push({name: 'newgoods'})
      },
      toOutTimeGoods() {
        this.$router.push({name: 'timeoutgoods'})
      },
      toHotSale() {
        this.$router.push({name: 'hotsale'})
      },
      toBrands() {
        this.$router.push({name: 'brandselected'})
      },
      toGorgeous() {
        this.$router.push({name: 'gorgeous'})
      },
      tolunbo() {
        this.$router.push({name: 'lunbo'})
      }
    },
    mounted() {
      new Swiper['default']('.swiper-container', {
        loop: true,
        autoplay:true,
        // 如果需要分页器
        pagination: {
          el: '.swiper-pagination',
        },

        // 如果需要滚动条
        scrollbar: {
          el: '.swiper-scrollbar',
        },
      })
    }
  }
</script>

<style scoped>

  .swiper-slide > img {
    width: 100%;
  }

  .wrapper {
    width: 100%;
    margin-bottom: 3.5rem;
    border-bottom: 1px solid #cccccc;
  }

  .wrap {
    width: 100%;
  }

  .cuxiao {
    height: 11rem;
    margin-top: 0.3rem;
  }

  .title {
    width: 100%;
    height: 2rem;
    line-height: 2rem;
    padding-left: 1rem;
    border-bottom: 1px gainsboro solid;
  }

  .title > img {
    width: 1rem;
  }

  .content {
    width: 100%;
    height: 9rem;
    margin-top: 0.2rem;
    border-bottom: 1px solid gainsboro;
    padding-bottom: 1rem;
    overflow: hidden;
  }

  .left {
    float: left;
    margin-left: 1rem;
  }

  .right {
    float: right;
    margin-right: 1rem;
  }

  .rigtop {
    height: 4.5rem;
  }

  .rigbottom {
    height: 4.5rem;
  }

  .contimg {
    width: 9rem;
  }

  .tuijian {
    width: 100%;
    margin-top: 2rem;
  }

  .bg {
    width: 100%;
  }

  .flex {
    display: flex;
  }

  .flex li {
    flex: 1;
    text-align: center;
  }

  .flex li > img {
    height: 5rem;
  }
</style>



