<template>
  <div class="wrapper">
    <div class="header">
      <router-link :to="{path: '/accountoption'}" class="router"><img src="../../../assets/img/2分类/返回.png" alt="" ></router-link>
      <span>个人信息</span>
    </div>
    <div class="content">
      <div>
        <span>头像</span><span><img src="../../../assets/img/5个人中心/设置/个人信息/touxiang.png" alt=""></span>
      </div>
      <div @click="toNickName"><span>昵称</span><span> 夏至未至 > </span></div>
      <div @click="toPhone"><span>手机号</span><span> 15012340809 > </span></div>
    </div>
  </div>
</template>
<script>
  export default {
    methods: {
      toInfo() {
        this.$router.push({name: 'accountinfo'});
      },
      toNickName() {
        this.$router.push({name: 'nickname'});
      },
      toPhone() {
        this.$router.push({name: 'phone'});
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span:nth-child(2) {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .header>span:nth-child(3) {
    font-size: 1rem;
    color: #ffffff;
    vertical-align: middle;
    position: absolute;
    right: 0.6rem;
    top: 1.2rem;
  }
  .content {
    width: 100%;
    overflow: scroll;
    margin-top: 3rem;
  }
  .content>div {
    padding: 0.6rem 0 0.6rem 0.6rem;
    background-color: #ffffff;
    overflow: hidden;
    border-bottom: 1px solid #cccccc;
  }
  .content>div>span:nth-child(1) {
    margin-left: 0.5rem;
  }
  .content>div:nth-child(1)>span:nth-child(1) {
    line-height: 3rem;
  }
  .content>div>span:nth-child(2) {
    float: right;
    margin-right: 1rem;
  }
  .content>div>span>img {
    width: 3rem;
  }
</style>



