<template>
  <div class="wrapper">
    <div class="header">
      <router-link :to="{path: '/personal'}" class="router"><img src="../../assets/img/2分类/返回.png" alt="" ></router-link>
      <span>身份实名认证</span>
      <span @click="toAddStatus">添加</span>
    </div>
    <div class="content">
      <div class="empty" v-if="isEmpty">
        <p>暂无身份信息,请添加</p>
        <div @click="toAddStatus">添加新身份信息</div>
      </div>
      <div class="noEmpty" v-if="!isEmpty">
        <div>
          <div>
            <p><span>李四</span><span>410781************</span></p>
            <img src="../../assets/img/5个人中心/实名认证/有内容/正.png" alt="">
            <img src="../../assets/img/5个人中心/实名认证/有内容/反.png" alt="">
          </div>
        </div>
        <div class="edit">
          <div><img src="../../assets/img/4购物车/write.png" alt=""><span @click="toAddStatus">编辑</span></div>
          <div><img src="../../assets/img/4购物车/delate.png" alt=""><span>删除</span></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        isEmpty: false,
        show2: -1
      }
    },
    methods: {
      toAddStatus() {
        this.$router.push({name: 'addstatus'});
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span:nth-child(2) {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .header>span:nth-child(3) {
    font-size: 1rem;
    color: #ffffff;
    vertical-align: middle;
    position: absolute;
    right: 0.6rem;
    top: 1.2rem;
  }
  .content {
    width: 100%;
    overflow: scroll;
    margin-top: 3rem;
  }
  .empty {
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    background: #ffffff;
    z-index: 600;
  }
  .empty>p {
    margin-top: 65%;
    margin-left: 30%;
    width: 10rem;
  }
  .empty>div {
    margin-top: 1rem;
    margin-left: 28%;
    width: 10rem;
    text-align: center;
    border: 1px solid #cccccc;
    border-radius: 0.5rem;
  }
  .noEmpty {
    background-color: #ffffff;
  }
  .noEmpty>div {
    width: 100%;
    margin-bottom: 1rem;
  }
  .noEmpty>div:nth-child(1)>div {
    padding: 0.5rem;
  }
  .noEmpty>div:nth-child(1)>div>p>span:nth-child(1) {
    margin-right: 3rem;
  }
  .noEmpty>div:nth-child(1)>div>img {
    width: 6.25rem;
    height: 3.75rem;
    margin-top: 1rem;
  }
  .noEmpty>div:nth-child(1)>div:nth-child(1)>img {
    margin-right: 1rem;
  }
  .edit {
    border-top: 1px solid #cccccc;
    position: relative;
    height: 2rem;
  }
  .edit>div>img {
    width: 1rem;
    height: 1rem;
    vertical-align: middle;
  }
  .edit>div:nth-child(1) {
    position: absolute;
    top: 0.5rem;
    right: 5rem;
  }
  .edit>div:nth-child(2) {
    position: absolute;
    top: 0.5rem;
    right: 1rem;
  }
</style>



