<template>
  <div class="wrapper">
    <span>阿拉灯</span>
    <input type="text" placeholder="输入商品关键词" @click="toSearch">
    <img src="../../assets/img/1首页/1推荐/形状16.png" alt="" class="news" @click="toNews">
  </div>
</template>
<script>
  export default {
    methods:{
      toSearch() {
        this.$router.push({name: 'search'});
      },
      toNews() {
        this.$router.push({name: 'news'});
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    background-color: rgb(229, 62, 66);
    height: 1.5rem;
    padding: 0.8rem 1rem;
    position: fixed;
    top: 0;
    z-index: 200;
  }
  span {
    width: 3.3rem;
    height: 1rem;
    font-family: HYy1gj;
    font-size: 1rem;
    font-weight: normal;
    font-stretch: normal;
    line-height: 1rem;
    letter-spacing: 0rem;
    color: #ffffff;
  }
  input {
    width: 12.5rem;
    height: 1.5rem;
    border-radius: 0.5rem;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
  }
  img {
    width: 1.5rem;
    height: 1.5rem;
    font-size: 0;
    vertical-align: middle;
  }
  input::-webkit-input-placeholder{
    width: 5.3rem;
    height: 1rem;
    padding-left: 1.5rem;
    font-family: MicrosoftYaHei;
    font-size: 1rem;
    line-height: 1rem;
    letter-spacing: 0rem;
    color: #e83d41;
  }
</style>



