<template>
  <div class="wrapper">
    <div class="header">
      <router-link :to="{path: '/option'}" class="router"><img src="../../../assets/img/2分类/返回.png" alt="" ></router-link>
      <span>账户设置</span>
    </div>
    <div class="content">
      <div @click="toInfo">
        <img src="../../../assets/img/5个人中心/设置/账户管理/phone.png" alt="">
        <span>手机号: 15012340809</span>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    methods: {
      toInfo() {
        this.$router.push({name: 'accountinfo'});
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span:nth-child(2) {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .header>span:nth-child(3) {
    font-size: 1rem;
    color: #ffffff;
    vertical-align: middle;
    position: absolute;
    right: 0.6rem;
    top: 1.2rem;
  }
  .content {
    width: 100%;
    overflow: scroll;
    margin-top: 3rem;
  }
  .content>div {
    padding: 0.6rem 0 0.6rem 0.6rem;
    background-color: #ffffff;
    overflow: hidden;
    border-bottom: 1px solid #cccccc;
  }
  .content>div>img {
    width: 0.7rem;
    vertical-align: middle;
  }
  .content>div>span {
    vertical-align: middle;
    margin-left: 1rem;
  }
</style>



