<template>
  <div class="body">
    <!--页眉-->
    <div class="top">
      <img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack">
      <span>消息中心</span>
    </div>
    <div class="content" v-for="obj in News">
      <div class="time">{{obj.time}}</div>
      <div class="txt">
        <h3>{{obj.title}}</h3>
        <div>{{obj.txt}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        News:[
          {
            time:'2017年06月12日11:41',
            title: '新品推荐',
            txt: '新品上市啦！时间不多了，赶紧行动起来吧！'
          },
          {
            time:'2017年06月05日21:01',
            title: '安全提醒',
            txt: '！近期有不法分子冒充阿拉灯客服进行诈骗，请认准阿拉灯官方客服电话4000123456'
          },
          {
            time:'2017年05月27日9:55',
            title: '收货啦',
            txt: '你的货物到站了，请注意查收哦~'
          },
        ]
      }
    },
    methods:{
      doBack() {
        this.$router.go(-1);
      },
    }
  }
</script>

<style scoped>
  .body {
    background:#f2f2f2;
    width: 100%;
    overflow: scroll;
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 300;
    text-align: center;
  }

  .top {
    width: 100%;
    height: 3rem;
    background: red;
    color: white;
    font-size: 1.5rem;
  }

  .top img {
    height: 1.5rem;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
  }

  .top span {
    margin-left: 7rem;
    margin-right: 5.5rem;
  }
  .time {
    font-family: MicrosoftYaHei;
    font-size: 1rem;
    margin-top: 1rem;
    letter-spacing: 0px;
    color: #808080;
  }
  .txt {
    width: 21rem;
    height: 10rem;
    background-color: #ffffff;
    border-radius: 0rem;
    border: solid 0rem #cccccc;
    margin: auto;
    padding-top: 0.5rem;
  }
  .txt h3{
    color: #4d4d4d;
  }
  .txt div{
    padding: 1rem;
    color: #808080;
  }
</style>



