<template>
  <div class="window">
    <div class="goodscontent" v-for="c in completeOrders">
      <div class="goodsID">
        <span>商品编号: {{c.id}}</span>
        <span>{{c.state}}</span>
      </div>
      <ul class="goodsInfo">
        <li>
          <div class="left" @click="doChoose(c)">
            <span v-if="isShow" class="DH" :class="{'active1': c.isactive}"></span>
            <img :src="c.img" alt="" class="img">
          </div>
          <div class="center" @click="toOrderDetails">
            <p>{{c.name}}</p>
            <p>规格: {{c.type}}</p>
            <p>¥ {{c.price}} <span>X {{c.count}}件</span></p>
          </div>
        </li>
      </ul>
      <div class="total">
        <p><span>实付:</span><span>¥ 166.06</span></p><p><span>发表心得</span></p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        completeOrders: [
          {
            id: 14852696746456,
            state: '已发货',
            img: require('../../../assets/img/4购物车/mifen.png'),
            name: '会呼吸丝滑蜜粉',
            type: '蜜粉  综合税0.2%',
            price: '166.06',
            count: 1,
            payType: '实付',
            time: '',
            operation: '确认收货',
            isactive: false
          }
        ],
        isShow: false,
        allChoose: false
      }
    },
    methods: {
      toOrderDetails() {
        this.$router.push({name: 'orderdetails'});
      },
      doChoose(c) {
        c.isactive = !c.isactive;
        for (let i = 0; i < this.completeOrders.length; i++) {
          if(this.completeOrders[i].isactive === false) {
            this.allChoose = false;
          } else {
            this.allChoose = true;
          }
          this.$root.showVM.$emit('chooseall1', this.allChoose)
        }
      }
    },
    created() {
      this.$root.showVM.$on('edit', (v) => {
        this.isShow = v;
      })
      this.$root.showVM.$on('chooseall', (v) => {
        for (let i = 0; i < this.completeOrders.length; i++) {
          this.completeOrders[i].isactive = v;
        }
      })
    }
  }
</script>
<style scoped>
  .goodscontent {
    width: 100%;
    overflow: scroll;
    background-color: #ffffff;
  }
  .goodsID {
    overflow: hidden;
    padding: 0.5rem 0.6rem;
    border-bottom: 1px solid #cccccc;
  }
  .goodsID>span:nth-child(1) {
    float: left;
  }
  .goodsID>span:nth-child(2) {
    float: right;
    color: red;
  }
  .goodsInfo {
    width: 100%;
    background-color: #ffffff;
  }
  .goodsInfo>li {
    border-bottom: 1px solid #cccccc;
    height: 6rem;
  }
  .left {
    width: 6rem;
    height: 100%;
    float: left;
    vertical-align: middle;
    padding-left: 0.6rem;
    position: relative;
  }
  .left>.DH {
    width: 1rem;
    height: 1rem;
    position: absolute;
    left: 0.6rem;
    top: 2.5rem;
    background: url("../../../assets/img/4购物车/huiduihao.png") center no-repeat;
    background-size: 100% 100%;
  }
  .left>.active1 {
    background: url("../../../assets/img/4购物车/duihao.png") center no-repeat;
    background-size: 100% 100%;
  }
  .left>.img {
    width: 3rem;
    height: 3rem;
    margin-top: 1.5rem;
    margin-left: 1.5rem;
  }
  .center {
    float: left;
    height: 100%;
    padding: 0.6rem 0 0 1.5rem;
  }
  .center>p:nth-child(1) {
    margin-top: 0.4rem;
    font-size: 1rem;
  }
  .center>p:nth-child(2) {
    margin-top: 0.3rem;
    font-size: 0.75rem;
    color: #666666;
  }
  .center>p:nth-child(3) {
    margin-top: 0.6rem;
    font-size: 0.75rem;
    color: red;
  }
  .center>P>span {
    color: #808080;
    margin-left: 1rem;
  }
  .total {
    padding: 0.6rem 0;
    width: 100%;
    overflow: hidden;
    vertical-align: middle;
  }
  .total>p:nth-child(1) {
    display: inline-block;
    float: left;
    margin-left: 0.6rem;
    margin-top: 0.1rem;
  }
  .total>p:nth-child(2) {
    display: inline-block;
    float: right;
    margin-right: 0.6rem;
  }
  .total>p:nth-child(1)>span:nth-child(1) {
    float: left;
  }
  .total>p:nth-child(1)>span:nth-child(2) {
    color: #e53e42;
  }
  .total>p:nth-child(2)>span {
    color: #ffffff;
    width: 5rem;
    font-size: 1rem;
    line-height: 1.5rem;
    background-color: #e53e42;
    text-align: center;
    border-radius: 0.6rem;
    display: inline-block;
  }
</style>



