<template>
  <div class="wrapper">
    <div class="header">
      <router-link :to="{path: '/order'}" class="router"><img src="../../assets/img/2分类/返回.png" alt="" ></router-link>
      <span>结算中心</span>
    </div>
    <div class="content">
      <div class="addressA">
        <div>
          <span>李四</span>
          <span>15012340708</span>
          <p>河南省新乡市红旗区南干道与新中大道交叉口B座10楼102室;邮编453000</p>
          <img src="../../assets/img/4购物车/修改地址.png" alt="" @click="toAddress">
        </div>
      </div>
      <div class="price">
        <div><span>本单请支付</span><span>&nbsp;&nbsp;&nbsp;¥ 226.03</span></div>
      </div>
      <div class="PT">
        <span>请使用第三方支付平台</span>
      </div>
      <div class="payWay" v-for="p in payWay" v-if="packUp" @click="doChoose(p.name)">
        <div>
          <img :src="p.img" alt="" class="img">
          <span>{{p.name}}</span>
          <span :class="{'duihao': duihao === p.name}" class="choose"></span>
        </div>
      </div>
      <div class="packUp" @click="doPackUp"><p>{{packUpStr}} ∧&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p></div>
      <div class="sure">
        <router-link :to="{path: '/countcenter'}" class="router2">确认支付</router-link>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        payWay: [
          {
           name:  '支付宝支付',
           img: require('../../assets/img/4购物车/zfb.png'),
          },
          {
            name:  '微信支付',
            img: require('../../assets/img/4购物车/wx.png'),
          }
        ],
        duihao: '支付宝支付',
        packUp: true,
        packUpStr: '收起'
      }
    },
    methods: {
      // 打勾效果
      doChoose(p) {
        this.duihao = p;
      },
      // 收起效果
      doPackUp() {
        this.packUp = !this.packUp;
        if (!this.packUp) {
          this.packUpStr = '展开';
        } else {
          this.packUpStr = '收起';
        }
      },
      toAddress() {
        this.$router.push({name: 'address'});
      },
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .content {
    width: 100%;
    margin-top: 3.2rem;
    overflow: scroll;
  }
  .addressA {
    padding: 1rem 0.6rem;
    background-size: 100% 100%;
    position: relative;
    background-color: #ffffff;
    margin-bottom: 0.5rem;
  }
  .addressA>div>span {
    font-size: 0.88rem;
    color: #4d4d4d;
    margin-right: 3rem;
  }
  .addressA>div>p {
    width: 13rem;
    margin-top: 1rem;
    font-size: 0.75rem;
    color: #808080;
  }
  .addressA>div>img {
    width: 2.5rem;
    height: 2.5rem;
    position: absolute;
    top: 2rem;
    right: 2rem;
  }
  .price, .payWay {
    width: 100%;
    background-color: #ffffff;
    border-bottom: 1px solid #cccccc;
  }
  .price>div, .payWay>div {
    padding: 0.6rem;
    overflow: hidden;
    position: relative;
  }
  .price>div>span:nth-child(1) {
    float: left;
  }
  .price>div>span:nth-child(2) {
    float: right;
    color: #e53e42;
  }
  .PT {
    width: 100%;
    height: 1rem;
    padding: 1rem 0.6rem;
    font-size: 0.88rem;
    margin-top: 0.5rem;
    border-bottom: 1px solid #cccccc;
    background-color: #ffffff;
  }
  .img {
    width: 1.5rem;
    height: 1.5rem;
    vertical-align: middle;
  }
  .choose {
    width: 1rem;
    height: 1rem;
    position: absolute;
    right: 1rem;
    top: 0.9rem;
    background: url("../../assets/img/4购物车/huiduihao.png") center no-repeat;
    background-size: 100% 100%;
  }
  .packUp {
    width: 100%;
    padding: 1rem 0.5rem;
    line-height: 0.5rem;
    border-bottom: 1px solid #cccccc;
    background-color: #ffffff;
    text-align: center;
  }
  .sure {
    padding: 0.5rem 0 1rem;
    margin-top: 3rem;
  }
  .sure>.router2 {
    display: block;
    width: 80%;
    background-color: #e53e42;
    color: #ffffff;
    margin: 0 auto;
    text-align: center;
    line-height: 1rem;
    border-radius: 0.5rem;
    padding: 0.3rem;
  }
  .duihao {
    background: url("../../assets/img/4购物车/duihao.png") center no-repeat;
    background-size: 100% 100%;
  }
</style>



