<template>
  <div class="window">
    <div v-if="!haveOrder" class="haveNotOrder">您暂时还没有订单哦~</div>
    <div class="goodscontent" v-for="(o, index) in orders" v-if="haveOrder">
      <div class="goodsID">
        <span>商品编号: {{o._id}}</span>
        <span>{{o.name}}</span>
      </div>
      <ul class="goodsInfo">
        <li>
          <div class="left" @click="doChoose(o, index)">
            <span v-if="isShow" class="DH" :class="{'active1': o.isactive}"></span>
            <img :src="o.img" alt="" class="img">
          </div>
          <div class="center" @click="toOrderDetails">
            <p>{{o.name}}</p>
            <p>规格: {{o.spec}}</p>
            <p>¥ {{o.price}} <span>X {{o.num}}件</span></p>
          </div>
        </li>
      </ul>
      <div class="total">
        <p>
          <span>{{o.name}}:</span>
          <span>¥ {{o.price * o.num}}</span>
        </p>
        <p>
          <span>{{o.name}}</span>
          <span>{{o.spec}}</span>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
  export default {
    data() {
      return {
        orders: [],
        haveOrder: true,
        isShow: false,
        allChoose: false,
        aindex: -1
      }
    },
    methods: {
      toOrderDetails() {
        this.$router.push({name: 'orderdetails'});
      },
      toCountCenter() {
        this.$router.push({name: 'countcenter'});
      },
      doChoose(o, index) {
        o.isactive = !o.isactive;
        for (let i = 0; i < this.orders.length; i++) {
          if(this.orders[i].isactive === false) {
            this.allChoose = false;
          } else {
            this.allChoose = true;
          }
          this.$root.showVM.$emit('chooseall1', this.allChoose);
        }
      }
    },
    created() {
      axios.post('/goods/getsCar').then((data) => {
        this.orders = data.data;
        if (this.orders.length === 0) {
          this.haveOrder = false;
        }
      });

      this.$root.showVM.$on('edit', (v) => {
        this.isShow = v;
      });
      this.$root.showVM.$on('chooseall', (v) => {
        for (let i = 0; i < this.orders.length; i++) {
          this.orders[i].isactive = v;
        }
      });
    }
  }
</script>
<style scoped>
  .window {
    width: 100%;
    height: 100%;
    overflow: scroll;
    background: #f2f2f2;
  }
  .haveNotOrder {
    width: 100%;
    margin: 15rem auto 25.3rem;
    text-align: center;
    color: #808080;
  }
  .goodscontent {
    width: 100%;
    background-color: #ffffff;
    margin-bottom: 1rem;
  }
  .goodsID {
    overflow: hidden;
    padding: 0.5rem 0.6rem;
    border-bottom: 1px solid #cccccc;
  }
  .goodsID>span:nth-child(1) {
    float: left;
  }
  .goodsID>span:nth-child(2) {
    float: right;
    color: red;
  }
  .goodsInfo {
    width: 100%;
    background-color: #ffffff;
  }
  .goodsInfo>li {
    border-bottom: 1px solid #cccccc;
    height: 6rem;
  }
  .left {
    width: 6rem;
    height: 100%;
    float: left;
    vertical-align: middle;
    padding-left: 0.6rem;
    position: relative;
  }
  .left>.DH {
    width: 1rem;
    height: 1rem;
    position: absolute;
    left: 0.6rem;
    top: 2.5rem;
    background: url("../../../assets/img/4购物车/huiduihao.png") center no-repeat;
    background-size: 100% 100%;
  }
  .left>.active1 {
    background: url("../../../assets/img/4购物车/duihao.png") center no-repeat;
    background-size: 100% 100%;
  }
  .left>.img {
    width: 3rem;
    height: 3rem;
    margin-top: 1.5rem;
    margin-left: 1.5rem;
  }
  .center {
    float: left;
    height: 100%;
    padding: 0.6rem 0 0 1.5rem;
  }
  .center>p:nth-child(1) {
    margin-top: 0.4rem;
    font-size: 1rem;
  }
  .center>p:nth-child(2) {
    margin-top: 0.3rem;
    font-size: 0.75rem;
    color: #666666;
  }
  .center>p:nth-child(3) {
    margin-top: 0.6rem;
    font-size: 0.75rem;
    color: red;
  }
  .center>P>span {
    color: #808080;
    margin-left: 1rem;
  }
  .total {
    padding: 0.6rem 0;
    width: 100%;
    overflow: hidden;
    vertical-align: middle;
  }
  .total>p:nth-child(1) {
    display: inline-block;
    float: left;
    margin-left: 0.6rem;
    margin-top: 0.1rem;
  }
  .total>p:nth-child(2) {
    display: inline-block;
    float: right;
    margin-right: 0.6rem;
  }
  .total>p:nth-child(1)>span:nth-child(1) {
    float: left;
  }
  .total>p:nth-child(1)>span:nth-child(2) {
    color: #e53e42;
  }
  .total>p:nth-child(2)>span:nth-child(1) {
    color: red;
    margin-right: 0.5rem;
    font-size: 0.75rem;
  }
  .total>p:nth-child(2)>span:nth-child(2) {
    color: #ffffff;
    width: 5rem;
    font-size: 1rem;
    line-height: 1.5rem;
    background-color: #e53e42;
    text-align: center;
    border-radius: 0.6rem;
    display: inline-block;
  }
</style>



