<template>
  <div class="wrapper">
    <div class="header">
      <router-link :to="{path: '/shopcarts'}" class="router"><img src="../../assets/img/2分类/返回.png" alt="" @click="sendToFooter"></router-link>
      <span>订单详情</span>
    </div>
    <div class="content">
      <div class="warning" v-if="addressA">
        根据海关要求,实名信息的真实姓名与支付姓名需一致,否则会导致清关失败无法发货
      </div>
      <div class="addressB" v-if="addressB">
        <router-link :to="{path: '/address'}" class="router3"><img src="../../assets/img/4购物车/添加.png" alt=""><span>添加收货地址</span></router-link>
      </div>
      <div class="addressA" v-if="addressA">
        <div>
          <span>李四</span>
          <span>15012340708</span>
          <p>河南省新乡市红旗区南干道与新中大道交叉口B座10楼102室;邮编453000</p>
          <img src="../../assets/img/4购物车/修改地址.png" alt="" @click="toAddress">
        </div>
      </div>
      <div class="personalInfoB" v-if="infoB"></div>
      <div class="personalInfoA" v-if="infoA">
        <div>
          <span>实名认证</span>
          <span>&nbsp;&nbsp;&nbsp;&nbsp;请确保姓名与支付方式的姓名一致&nbsp;</span>
          <p><span>李四</span><span>4107*************5</span></p>
          <div><img src="../../assets/img/4购物车/write.png" alt=""><span>编辑</span></div>
          <div><img src="../../assets/img/4购物车/delate.png" alt=""><span>删除</span></div>
        </div>
      </div>
      <div class="orderGoods">
        <span>海外直邮订单</span>
      </div>
      <ul class="goods">
        <li v-for="g in goods" v-if="g.packUp" @click="toProduct">
          <div class="left">
            <img :src="g.img" alt="">
          </div>
          <div class="center">
            <p>{{g.name}}</p>
            <p>规格: {{g.type}}</p>
            <p>¥ {{g.price}}</p>
          </div>
        </li>
      </ul>
      <div class="packUp" @click="doPackUp()"><p>{{packUpStr}} ∧&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p></div>
      <ul class="cost">
        <li><span>商品金额</span><span>¥ 166.03</span></li>
        <li><span>订单邮费</span><span>¥ 50</span></li>
        <li><span>关税</span><span>¥ 10</span></li>
        <li><span>订单金额</span><span>¥ 226.03</span></li>
      </ul>
      <div class="remind">
        <div><span>安全提醒:</span><span>付款成功后,阿拉灯不会以付款异常,卡单,系统升级为由联系您.请勿泄漏您的银行卡号,手机验证码,否则会造成钱款的损失.如有疑问请咨询客服,谨防电话诈骗!</span></div>
      </div>
      <div class="way"><div><span>配送方式:</span><span>日本直邮</span></div></div>
      <div class="price">
        <div><span>共1件商品</span><span>应付:<span>&nbsp;&nbsp;&nbsp;¥ 226.03</span></span></div>
      </div>
      <div class="law">
        <div><img src="../../assets/img/4购物车/duihao.png" alt="">
        <span>本人同意并接受<span style="color: red">《个人委托协议》</span>和<span style="color: red">《用户协议》</span></span></div>
      </div>
      <div class="sure">
        <router-link :to="{path: '/countcenter'}" class="router2">确认</router-link>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        show2: 1,
        addressA: true,
        addressB: false,
        infoA: true,
        infoB: false,
        goods: [
          {
            name: '会呼吸丝滑蜜粉',
            img: require("../../assets/img/4购物车/mifen.png"),
            type: '蜜粉',
            price: 166.06,
            packUp: true
          },
          {
            name: '会呼吸丝滑蜜粉',
            img: require("../../assets/img/4购物车/mifen.png"),
            type: '蜜粉',
            price: 166.06,
            packUp: false
          },
          {
            name: '会呼吸丝滑蜜粉',
            img: require("../../assets/img/4购物车/mifen.png"),
            type: '蜜粉',
            price: 166.06,
            packUp: false
          }
        ],
        personInfo: [],
        otherGoods: [],
        otherCount: 0,
        packUpStr: '展示其余商品'
      }
    },
    created() {
      // 截取其余商品
      for (let i = 0; i < this.goods.length; i++) {
        this.otherGoods = this.goods.slice(1, this.goods.length);
      }
      this.otherCount = this.otherGoods.length;
      this.packUpStr = '展示其余('+ this.otherCount +')件商品';
      this.$root.showVM.$on('personInfo', (v) => {
        this.personInfo = v;
      })
    },
    methods: {
      sendToFooter() {
        this.$root.showVM.$emit('isshow', this.show2);
      },
      // 收起效果
      doPackUp() {
        for (let i = 0; i < this.otherGoods.length; i++) {
          this.otherGoods[i].packUp = !this.otherGoods[i].packUp;
          if (!this.otherGoods[i].packUp) {
            this.packUpStr = '展示其余('+ this.otherCount +')件商品';
          } else {
            this.packUpStr = '收起其余商品';
          }
        }
      },
      toAddress() {
        this.$router.push({name: 'address'});
      },
      toProduct() {
        this.$router.push({name: 'product'});
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .content {
    width: 100%;
    margin-top: 3.2rem;
    overflow: scroll;
  }
  .warning {
    background-color: #666666;
    color: #ffffff;
    font-size: 0.75rem;
    padding: 0.3rem 0.6rem;
  }
  .addressB {
    background-color: #ffffff;
    padding: 1rem 0.6rem;
    background: url("../../assets/img/4购物车/蓝杠.png") center no-repeat;
    background-size: 100% 100%;
  }
  .addressB>.router3 {
    display: block;
  }
  .addressB>.router3>span {
    font-size: 0.88rem;
    margin-left: 1rem;
    color: #808080;
  }
  .addressB>.router3>img {
    width: 1.5rem;
    height: 1.5rem;
    vertical-align: middle;
  }
  .addressA {
    padding: 1rem 0.6rem;
    background: url("../../assets/img/4购物车/蓝杠.png") center no-repeat;
    background-size: 100% 100%;
    position: relative;
    background-color: #ffffff;
    margin-bottom: 0.5rem;
  }
  .addressA>div>span {
    font-size: 0.88rem;
    color: #4d4d4d;
    margin-right: 3rem;
  }
  .addressA>div>p {
    width: 13rem;
    margin-top: 1rem;
    font-size: 0.75rem;
    color: #808080;
  }
  .addressA>div>img {
    width: 2.5rem;
    height: 2.5rem;
    position: absolute;
    top: 2rem;
    right: 2rem;
  }
  .personalInfoB {
    width: 100%;
    height: 4rem;
    background: url("../../assets/img/4购物车/五角星.png") no-repeat center;
    background-size: 100% 100%;
    padding: 0.6rem;
    background-color: #ffffff;
    margin-top: 0.5rem;
  }
  .personalInfoA {
    background: url("../../assets/img/4购物车/五角星.png") no-repeat center;
    background-size: 100% 100%;
    padding: 0.6rem;
    background-color: #ffffff;
  }
  .personalInfoA>div {
    position: relative;
    padding-bottom: 1.5rem;
  }
  .personalInfoA>div>span:nth-child(1) {
    font-size: 1rem;
    margin-right: 0.1rem;
  }
  .personalInfoA>div>span:nth-child(2) {
    font-size: 0.75rem;
    line-height: 1.5rem;
    color: #e53e42;
    background: url("../../assets/img/4购物车/圆角矩形4.png") no-repeat center;
    background-size: 100% 100%;
  }
  .personalInfoA>div>p>span {
    font-size: 0.88rem;
    color: #4d4d4d;
    margin-right: 3rem;
  }
  .personalInfoA>div>div>img {
    width: 1rem;
    height: 1rem;
    vertical-align: middle;
  }
  .personalInfoA>div>div:nth-child(4) {
    position: absolute;
    bottom: 0;
    right: 5rem;
  }
  .personalInfoA>div>div:nth-child(5) {
    position: absolute;
    bottom: 0;
    right: 1rem;
  }
  .personalInfoA>div>div>span {
    font-size: 0.75rem;
    color: #4d4d4d;
    vertical-align: middle;
  }
  .orderGoods {
    width: 100%;
    height: 1rem;
    padding: 1rem 0.6rem;
    margin-top: 0.5rem;
    border-bottom: 1px solid #cccccc;
    background-color: #ffffff;
  }
  .goods {
    width: 100%;
    background-color: #ffffff;
  }
  .goods>li {
    width: 100%;
    border-bottom: 1px solid #cccccc;
    height: 6rem;
    overflow: hidden;
  }
  .left {
    width: 6rem;
    height: 100%;
    float: left;
    vertical-align: middle;
    padding-left: 0.6rem;
  }
  .left>img {
    width: 3rem;
    height: 3rem;
    margin-top: 1.5rem;
    margin-left: 1.5rem;
  }
  .center {
    float: left;
    height: 100%;
    padding: 0.6rem 0 0.6rem 1.5rem;
  }
  .center>p:nth-child(1) {
    margin-top: 0.4rem;
    font-size: 1rem;
  }
  .center>p:nth-child(2) {
    margin-top: 0.3rem;
    font-size: 0.75rem;
    color: #666666;
  }
  .center>p:nth-child(3) {
    margin-top: 0.6rem;
    font-size: 0.75rem;
    color: red;
  }
  .packUp {
    width: 100%;
    padding: 1rem 0.5rem;
    line-height: 0.5rem;
    border-bottom: 1px solid #cccccc;
    background-color: #ffffff;
    text-align: center;
  }
  .packUp>p {
    margin: 0 auto 0.5rem;
  }
  .cost {
    width: 100%;
    background-color: #ffffff;
    margin-top: 0.5rem;
  }
  .cost>li {
    width: 100%;
    height: 1rem;
    padding: 1rem 2rem;
    overflow: hidden;
    border-bottom: 1px solid #cccccc;
    position: relative;
  }
  .cost>li>span:nth-child(1) {
    font-size: 1rem;
    text-align: left;
    color: #4d4d4d;
    float: left;
  }
  .cost>li>span:nth-child(2) {
    font-size: 1rem;
    text-align: right;
    color: red;
    float: right;
    margin-right: 3rem;
  }
  .remind, .way, .price, .law, .sure {
    width: 100%;
    background-color: #ffffff;
  }
  .remind>div, .way>div, .price>div, .law>div {
    padding: 0.6rem;
    overflow: hidden;
  }
  .remind>div>span:nth-child(1) {
    font-size: 1rem;
    color: #e53e42;
  }
  .remind>div>span:nth-child(2) {
    font-size: 0.75rem;
    color: #4d4d4d;
  }
  .way {
    margin-top: 0.5rem;
  }
  .way>div>span, .price>div>span {
    font-size: 0.88rem;
    color: #4d4d4d;
  }
  .way>div>span:nth-child(1), .price>div>span:nth-child(1) {
    float: left;
  }
  .way>div>span:nth-child(2), .price>div>span:nth-child(2) {
    float: right;
  }
  .price {
    margin-top: 1rem;
  }
  .price>div>span>span {
    color: #e53e42;
  }
  .law {
    border-top: 1px solid #cccccc;
  }
  .law>div>img {
    width: 1rem;
    height: 1rem;
    vertical-align: middle;
  }
  .law>div>span {
    font-size: 0.88rem;
    color: #4d4d4d;
  }
  .sure {
    padding: 0.5rem 0 1rem;
  }
  .sure>.router2 {
    display: block;
    width: 80%;
    background-color: #e53e42;
    color: #ffffff;
    margin: 0 auto;
    text-align: center;
    line-height: 1rem;
    border-radius: 0.5rem;
    padding: 0.3rem;
  }
</style>



