<template>
    <div class="body">
      <!--页眉-->
      <div class="top">
        <img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack">
        <span>打造玫瑰嫩滑肌肤</span>
        <img src="../../assets/img/3断货王/断货王/share.png" alt="">
      </div>
      <div class="bigpic">
        <img src="../../assets/img/1首页/1推荐/lunbodazao.png" alt="">
      </div>
      <div class="brandtxt">
        Rose Hydrosed是一款来自澳大利亚的护肤产品，主
        要功能为深层滋润肌肤，锁住水分，打造玫瑰般娇嫩肌
        肤...
        <br>
        <br>
        下面为您推荐一些产品
      </div>
      <div class="tiao"></div>
      <div class="title">
        <div><img src="../../assets/img/1首页/轮播详情/no1.png" alt=""></div>
        <div>防晒类     第1位</div>
      </div>
      <div class="txt">
        <div class="left"><img src="../../assets/img/1首页/1推荐/xianshiduanhuo.png" alt=""></div>
        <div class="right">
          <h3>美肤宝</h3>
          <div >守护肌肤预防紫外线所引发的斑点以及胶原蛋
白变形等，是具有抗老保养效果的全效防晒霜</div>
          <div class="buy">立即购买</div>
        </div>
      </div>
      <div class="tiao"></div>
      <div class="good">
        <div><img :src="goods[0].img" alt=""></div>
        <div>{{goods[0].name}}</div>
        <div>{{goods[0].price}}</div>
        <span>立即购买</span>
      </div>
      <div class="good">
        <div><img :src="goods[0].img" alt=""></div>
        <div>{{goods[0].name}}</div>
        <div>{{goods[0].price}}</div>
        <span>立即购买</span>
      </div>
      <div class="good">
        <div><img :src="goods[0].img" alt=""></div>
        <div>{{goods[0].name}}</div>
        <div>{{goods[0].price}}</div>
        <span>立即购买</span>
      </div>
      <div class="good">
        <div><img :src="goods[0].img" alt=""></div>
        <div>{{goods[0].name}}</div>
        <div>{{goods[0].price}}</div>
        <span>立即购买</span>
      </div>
    </div>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
    export default {
      data() {
        return {
          goods: []
        }
      },
      methods: {
        doBack() {
          this.$router.go(-1);
        }
      },
      created() {
        axios.post('/goods/getHots', {
          kind: '超值热卖'
        }).then((data) => {
          this.goods = data.data;
          console.log(this.goods);
        })
      }
    }
</script>

<style scoped>
  .body {
    background: white;
    width: 100%;
    overflow: scroll;
    position: absolute;
    top: 0;
    margin-bottom: 4rem;
    z-index: 300;
  }

  .top {
    width: 100%;
    height: 3rem;
    background: red;
    color: white;
    font-size: 1.5rem;
  }

  .top img {
    height: 1.5rem;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
  }

  .top span {
    margin-left: 3rem;
    margin-right: 1rem;
  }

  .bigpic img {
    width: 100%;
  }

  .brandtxt {
    width: 92%;
    height: 8rem;
    padding: 1rem;
  }
  .title{
    overflow: hidden;
    color: #c46c25;
    padding: 0.5rem;
  }
  .title div{
    float: left;
  }
  .title div:nth-child(2){
    width: 16rem;
    margin: 1rem;
    font-size: 1.2rem;
    border-bottom:  6px double #c46c25;
  }
  .title img{
    height: 3rem;
    vertical-align: middle;
  }
  .tiao{
    width: 100%;
    height: 0.5rem;
    background: #feeaf5;
  }
  .txt{
    display: flex;
  }
  .left{
    flex: 1;
    padding: 0.5rem;
  }
  .right{
    flex: 2;
    color: #808080;
  }
  .txt img{
    height: 7rem;
  }
  .buy{
    width: 5rem;
    background-color: #e53e42;
    color: white;
    text-align: center;
    font-size: 0.7rem;
    padding: 0.3rem;
  }
  .good{
    width: 11rem;
    text-align: center;
    border: gainsboro 1px solid;
    padding: 0.5rem;
    display: inline-block;
  }
  .good img{
    height: 8rem;
  }
  .good div:nth-child(2){
    padding: 0.5rem;
    color: #4d4d4d;
    border-top: 1px solid gainsboro;
  }
  .good div:nth-child(3){
    color: red;
    padding: 0.2rem;
  }
  .good span{
    border-radius: 1rem;
    color: white;
    background: red;
    font-size: 0.7rem;
    padding: 0.3rem;
  }
</style>



