          <li>根据海关规定，购买跨境商品需要办理清关手续，需要您上传身份证信息用于商品
            入境申报真实的姓名和身份信息才能确保您的包裹顺利通过海关检查</li>
          <li>声明：阿拉灯负责提交信息，并对您的身份信息保密，绝不传播或用于其他地方
            请放心填写。</li>
        </ul>
      </div>
      <div class="submit">
        <div>提交</div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {

      }
    },
    methods: {
      goBack() {
        window.history.go(-1);
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span:nth-child(2) {
