    color: #ffffff;
    vertical-align: middle;
  }
  .header>span:nth-child(3) {
    font-size: 1rem;
    color: #ffffff;
    vertical-align: middle;
    position: absolute;
    right: 0.6rem;
    top: 1.2rem;
  }
  .content {
    width: 100%;
    overflow: scroll;
    margin-top: 3rem;
  }
  input {
    width: 100%;
    padding: 0.6rem;
    appearance: none;
    border-top: 1px solid #cccccc;
    border-left: none;
    border-bottom: none;
    border-right: none;
    outline: none;
    font-size: 1rem;
  }
  .info {
    padding: 0.3rem 0;
  }
  .info>span:nth-child(2), .info>span:nth-child(6) {
    color: red;
    margin-left: 1rem;
    font-size: 0.75rem;
  }
  .info>span:nth-child(1), .info>span:nth-child(5) {
