  }
  .leftbottom {
    margin-left: 1rem;
  }
  .lefttop>p, .righttop>p {
    margin: 0.5rem 0;
  }
  .rightbottom {
    margin-left: 4rem;
  }
  .lefttop, .righttop, .leftbottom, .rightbottom {
    display: inline-block;
  }
  .lefttop>img, .righttop>img {
    width: 8.75rem;
    height: 6.25rem;
  }
  .leftbottom>img, .rightbottom>img {
    width: 6.88rem;
    height: 8.25rem;
  }
  .why>div {
    color: #e53e42;
    background-color: #f9d3d4;
    text-align: center;
  }
  .why>ul {
    list-style: circle;
    padding: 0.5rem;
  }
  .why>ul>li {
    font-size: 0.5rem;
    color: #666666;
  }
  .submit {
    background-color: #ffffff;
    padding: 1.5rem 0;
  }
  .submit>div {
    color: #ffffff;
    background-color: #e53e42;
    border-radius: 0.5rem;
    width: 6rem;
    text-align: center;
    margin: 0 auto;
  }
</style>



