<template>
  <div class="wrapper">
    <div class="header">
      <span>注册</span>
    </div>
    <div class="content">
      <div class="form">
        <p class="warning">我们将发送一段验证码到您的手机上,请查收</p>
        <p><input type="text" placeholder=" 请输入手机号" v-model="numb" @click="reInput"><span v-if="numisShow" @click="delNum">X</span></p>
        <p><input type="text" placeholder=" 请输入手机验证码" v-model="code"><span v-if="passisShow" @click="delPass">X</span></p><span class="getnumber">获取验证码</span>
        <p><input type="text" placeholder=" 请输入密码" v-model="pass"><span v-if="codeisShow">X</span></p>
        <p class="name"><input type="text" placeholder=" 请输入昵称" v-model="name"></p>
        <p><span @click="toPersonal">取消</span><span @click="doRegis">完成注册</span></p>
      </div>
      <div class="mask" v-if="isShow"></div>
      <div class="success" v-if="isShow">
        注册成功!
        <p @click="doLogin">登录</p>
      </div>
      <p class="error" v-if="isShow2">该用户已存在,请重新注册</p>
    </div>
  </div>

</template>
<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
  export default {
    data() {
      return {
        numb: '',
        pass: '',
        code: '',
        name: '',
        numisShow: false,
        passisShow: false,
        codeisShow: false,
        show1: 1,
        isShow: false,
        isShow2: false
      }
    },
    methods: {
      delNum() {
        this.numb = '';
      },
      delPass() {
        this.pass = '';
      },
      delCode() {
        this.code = '';
      },
      reInput() {
        this.isShow2 = false;
      },
      doRegis() {
        axios.post('/goods/regis', {
          ph:this.numb,
          pw:this.pass,
          pn:this.name
        }).then((data) => {
          if (data.data.error === '注册成功') {
            console.log(data)
            this.isShow = true;
          } else {
            this.isShow2 = true;
          }
        })
      },
      doLogin() {
        this.$root.showVM.$emit('isshow', this.show1);
        this.$router.push({name: 'personal'});
      },
      toPersonal() {
        this.$root.showVM.$emit('isshow', this.show1);
        this.$router.push({name: 'personal'});
      }
    },
    watch: {
      numb: function (v1, v2) {
        if (v1 === '') {
          this.numisShow = false;
        } else {
          this.numisShow = true;
        }
      },
      pass: function (v1, v2) {
        if (v1 === '') {
          this.passisShow = false;
        } else {
          this.passisShow = true;
        }
      },
      code: function (v1, v2) {
        if (v1 === '') {
          this.codeisShow = false;
        } else {
          this.codeisShow = true;
        }
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .header>span {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .content {
    width: 100%;
    overflow: scroll;
    margin-top: 3rem;
  }
  .form {
    width: 80%;
    margin: 1rem auto;
    overflow: hidden;
  }
  .form>p:nth-child(2), .form>p:nth-child(5){
    width: 99%;
    margin-top: 1rem;
  }
  .form>p:nth-child(5) {
    float: left;
  }
  .form>p:nth-child(3) {
    width: 60%;
    margin-top: 1rem;
    float: left;
  }
  .form>p:nth-child(2), .form>p:nth-child(3), .form>p:nth-child(5) {
    border: 1px solid #cccccc;
    border-radius: 0.5rem;
  }
  .name {
    border: 1px solid #cccccc;
    border-radius: 0.5rem;
    float: left;
    width: 99%;
    margin-top: 1rem;
  }
  .form>p:nth-child(3)>input {
    width: 90%;
  }
  .form>p:last-child {
    overflow: hidden;
    padding-top: 1rem;
    width: 100%;
  }
  .form>p:last-child>span {
    padding: 0.5rem 0;
    width: 6rem;
    display: inline-block;
    text-align: center;
    border-radius: 0.5rem;
  }
  .form>p:last-child>span:nth-child(1) {
    border: 1px solid #cccccc;
    float: left;
  }
  .form>p:last-child>span:nth-child(2) {
    background-color: red;
    color: #ffffff;
    float: right;
  }
  input {
    width: 90%;
    padding: 0.6rem 0;
    appearance: none;
    border: none;
    outline: none;
  }
  .warning {
    font-size: 0.5rem;
    text-align: center;
    color: #808080;
    margin-top: 0.5rem;
  }
  .getnumber {
    border: 1px solid #cccccc;
    border-radius: 0.5rem;
    float: right;
    width: 30%;
    padding: 0 0.5rem;
    margin-top: 1.25rem;
  }
  .mask {
    position: absolute;
    background-color: #000000;
    opacity: 0.45;
    top: 0;
    bottom: -18.6rem;
    left: 0;
    right: 0;
    z-index: 700;
  }
  .success {
    width: 80%;
    position: absolute;
    bottom: -1rem;
    background-color: #ffffff;
    z-index: 850;
    border-radius: 0.5rem;
    left: 10%;
    padding: 2rem 0;
    text-align: center;
  }
  .success>p {
    width: 5rem;
    margin: 1rem auto 0;
    color: #ffffff;
    padding: 0.5rem 1rem;
    background-color: red;
  }
  .error {
    text-align: center;
    color: red;
    font-size: 1rem;
  }
</style>



