<template>
  <div class="wrapper">
    <div class="header">
      <a class="router" @click="goBack"><img src="../../assets/img/2分类/返回.png" alt="" ></a>
      <span>收货地址</span>
      <router-link :to="{path: '/newaddress'}" class="router1">添加新地址</router-link>
    </div>
    <div class="content">
      <div v-if="isEmpty" class="empty">
        <div>
          <img src="../../assets/img/5个人中心/收货地址/收货地址1/地图.png" alt="">
        </div>
        <router-link :to="{path: '/newaddress'}" class="router2">添加新地址</router-link>
      </div>
      <div class="addressA" v-if="!isEmpty" v-for="(a, index) in addressData">
        <div>
          <span>{{a.name}}</span>
          <span>{{a.number}}</span>
          <p>{{a.address}}</p>
          <div class="edit">
            <div class="default" @click="setDefault(a, index)" :class="{'active': isActive === a.id}">
              <div></div>
              <span>{{a.activeSrc}}</span>
            </div>
            <div @click="toNewAddress"><img src="../../assets/img/4购物车/write.png" alt=""><span>编辑</span></div>
            <div @click="delAddress(a, index)"><img src="../../assets/img/4购物车/delate.png" alt=""><span>删除</span></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    created() {
        console.log(this.$route.params);
    },
    data() {
      return {
        isEmpty: false,
        addressData: [
          {
            id: 1,
            name: '李四',
            number: '15012340708',
            address: '河南省新乡市红旗区南干道与新中大道交叉口B座10楼102室;邮编453000',
            default: true,
            activeSrc: '设为默认'
          },
          {
            id: 2,
            name: '李四',
            number: '15012340708',
            address: '河南省新乡市红旗区南干道与新中大道交叉口B座10楼103室;邮编453000',
            default: false,
            activeSrc: '设为默认'
          }
        ],
        isActive: 1,
      }
    },
    methods: {
      setDefault(a, index) {
        this.isActive = a.id;
        for (let i = 0; i < this.addressData.length; i++) {
          if (i === index) {
            this.addressData[i].default = true;
            this.addressData[i].activeSrc = '默认地址';
          } else {
            this.addressData[i].default = false;
            this.addressData[i].activeSrc = '设为默认';
          }
        }
      },
      delAddress(a, index) {
        for (let i = 0; i < this.addressData.length; i++) {
          this.addressData.splice(this.addressData[index], 1)
        }
      },
      toNewAddress() {
        this.$router.push({name: 'newaddress'});
      },
      goBack() {
        window.history.go(-1);
      }
    },
    created() {
      for (let i = 0; i < this.addressData.length; i++) {
        if (this.addressData[i].default === true) {
          this.addressData[i].activeSrc = '默认地址'
        }
      }
      this.$root.showVM.$emit('personInfo', this.addressData);
    }
//    computed: {
//      getName() {
//        console.log(this.$route);
//        return this.$route.params.realname;
//      },
//      getPhoneNumber() {
//        console.log(this.$route);
//        return this.$route.params.phoneNumber;
//      },
//      getCity() {
//        console.log(this.$route);
//        return this.$route.params.city;
//      },
//      getAddress() {
//        console.log(this.$route);
//        return this.$route.params.address;
//      },
//      getPostcode() {
//        console.log(this.$route);
//        return this.$route.params.postcode;
//      }
//    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    position: relative;
    background-color: #f2f2f2;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .router1 {
    color: #ffffff;
    position: absolute;
    font-size: 1rem;
    right: 0.3rem;
    top: 1rem;
  }
  .content {
    width: 100%;
    margin-top: 3.2rem;
    overflow: scroll;
  }
  .empty {
    margin: 8rem auto 0;
    text-align: center;
  }
  .empty>div>img {
    width: 6.25rem;
    height: 6.25em;
  }
  .empty>.router2 {
    display: block;
    color: #4d4d4d;
    font-size: 1rem;
    width: 7rem;
    padding: 0.6rem 1rem;
    border: 1px solid #cccccc;
    text-align: center;
    border-radius: 1rem;
    margin: 1rem auto;
  }
  .content>.addressA {
    padding: 1rem 0.6rem;
    background-size: 100% 100%;
    position: relative;
    background-color: #ffffff;
    margin-bottom: 0.5rem;
  }
  .content>.addressA>div>span {
    font-size: 0.88rem;
    color: #4d4d4d;
    margin-right: 3rem;
  }
  .content>.addressA>div>p {
    margin-top: 1rem;
    margin-bottom: 0.6rem;
    font-size: 0.75rem;
    color: #808080;
  }
  .content>.addressA>div>img {
    width: 2.5rem;
    height: 2.5rem;
    position: absolute;
    top: 2rem;
    right: 2rem;
  }
  .edit {
    border-top: 1px solid #cccccc;
    position: relative;
    height: 2rem;
  }
  .edit>div>img {
    width: 1rem;
    height: 1rem;
    vertical-align: middle;
  }
  .default>div {
    display: inline-block;
    vertical-align: middle;
    width: 1rem;
    height: 1rem;
    background: url("../../assets/img/5个人中心/收货地址/收货地址1/默认地址1.png") no-repeat center;
    background-size: 100% 100%;
  }
  .edit>div:nth-child(1) {
    position: absolute;
    top: 0.5rem;
    left: 0;
  }
  .edit>div:nth-child(2) {
    position: absolute;
    top: 0.5rem;
    right: 5rem;
  }
  .edit>div:nth-child(3) {
    position: absolute;
    top: 0.5rem;
    right: 1rem;
  }
  .edit>div>span {
    font-size: 0.75rem;
    color: #4d4d4d;
    vertical-align: middle;
  }
  .active>div {
    background: url("../../assets/img/5个人中心/收货地址/收货地址1/默认地址.png") no-repeat center;
    background-size: 100% 100%;
  }
</style>



