<template>
  <div class="body">
    <!--分栏-->
    <div class="segment">
      <ul class="segUl">
        <li @click="toRecommend">推荐</li>
        <li @click="toMiss">贼惦记</li>
        <li @click="toNicefood">美食</li>
        <li @click="toMomAndBaby">母婴</li>
      </ul>
    </div>
    <!--分栏具体内容-->
    <div class="content">
     <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {}
    },
    methods: {
      toRecommend() {
        this.$router.push({name: 'homepage/recommend'});
      },
      toMiss() {
        this.$router.push({name: 'homepage/miss'});
      },
      toNicefood() {
        this.$router.push({name: 'homepage/nicefood'});
      },
      toMomAndBaby() {
        this.$router.push({name: 'homepage/momandbaby'});
      }
    }
  }
</script>

<style scoped>
 .body{
   width: 100%;
   margin-top: 3rem;
   overflow: scroll;
   margin-bottom: 4rem;
 }
  .segUl {
    width: 100%;
    height: 2rem;
    display: flex;
    text-align: center;
    background: #ffffff;
    border-bottom: 1px solid gainsboro;
  }

  .segUl li {
    flex: 1;
    font-size: 1rem;
    line-height: 2rem;
  }

  .segUl li:hover {
    color: red;
    border-bottom: 1px red solid;
  }
  .content{
    width: 100%;
  }
</style>



