<template>
  <div class="body">
    <!--页眉-->
    <div class="top">
      <span><img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack" ></span>
      <span> <input type="text" placeholder="输入商品关键词" v-model="data"></span>
      <span class="span1" @click="doSearch">搜索</span>
    </div>
    <div class="found">
      <p>亲们都在找:</p>
    </div>
    <div class="names">
      <div>眼霜</div>
      <div>防晒</div>
      <div>精华</div>
      <div>面膜</div>
    </div>
    <div class="found">
      <p>最近搜索:</p>
    </div>
    <div class="txt" v-for=" item in dataArr">
      <span  class="span2" @click="dotxt(item.goodname)">{{item.goodname}}<br></span>
    </div>
    <div class="out" @click="doClean">清空历史</div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
  export default {
    data() {
      return {
       data:'',
       dataArr:[],
       name:[]
      }
    },
    methods: {
      doBack() {
        this.$router.push({name: 'homepage'});
      },
      doSearch() {
        axios.post('/goods/dosearch', {
          goodname:this.data
        }).then((data) => {
          this.name = data.data;
          console.log(data.data);
          this.$router.push({name: 'searchtxt',params: {name: JSON.stringify(this.name) }});
        });
        axios.post('/goods/getRecord', {name:this.data});
      },
      doClean() {
        axios.post('/goods/cleanRecords');
        this.dataArr = [];
        return this.dataArr;
      },
      dotxt(e) {
        this.data = e
      }
    },
    created() {
      axios.post('/goods/getRecords').then((data) => {
       this.dataArr = data.data
      });
    }
  }
</script>

<style scoped>
  .body {
    background: white;
    width: 100%;
    overflow: scroll;
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 300;
  }

  .top {
    width: 100%;
    background-color: rgb(229, 62, 66);
    height: 1.5rem;
    padding: 0.8rem 1rem;
    position: fixed;
    top: 0;
    z-index: 200;
  }

  .top span {
    width: 3.3rem;
    height: 1rem;
    font-family: HYy1gj;
    font-size: 1rem;
    font-weight: normal;
    font-stretch: normal;
    line-height: 1rem;
    letter-spacing: 0rem;
    color: #ffffff;
  }

  input {
    width: 12.5rem;
    height: 1.5rem;
    border-radius: 0.5rem;
    margin-left: 2.5rem;
    margin-right: 1.5rem;
  }

  .top img {
    width: 1.5rem;
    height: 1.5rem;
    font-size: 0;
    vertical-align: middle;
  }

  input::-webkit-input-placeholder {
    width: 5.3rem;
    height: 1rem;
    padding-left: 1.5rem;
    font-family: MicrosoftYaHei;
    font-size: 1rem;
    line-height: 1rem;
    letter-spacing: 0rem;
    color: #e83d41;
  }

  .found {
    width: 100%;
    height: 1rem;
    margin-top: 3rem;
    background: gainsboro;
    color: grey;
    padding: 1rem;
  }

  .names {
    overflow: hidden;
  }

  .names div {
    float: left;
    margin-left: 1rem;
    margin-top: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;
    border-radius: 3rem;
    border: 1px solid gainsboro;
  }

  .txt {
    width: 100%;
    margin-bottom: 2rem;
    padding: 1rem;
  }

  .txt span {
    margin-left: 1rem;
  }

  .out {
    width: 14rem;
    border: 1px solid gainsboro;
    text-align: center;
    margin: auto;
    color: grey;
    font-weight: bold;
  }
  .span2{
    padding: 0.5rem;
    border: 1px gainsboro solid;
    display: inline-block;
    margin: 0.5rem;
    border-radius: 0.5rem;
    background: gainsboro;
    color: black;
  }
</style>



