<template>
  <div class="wrapper">
    <div class="bigPic"><a class="router" @click="doClick"></a></div>
    <div class="goods">
      <div v-for="g in goods">
        <img :src="g.img" alt="">
        <p>{{g.name}}</p>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'
  export default {
    data() {
      return {
        show1: 1,
        allgoods: [],
        goods: [],
        getKind1: ''
      }
    },
    props: {
      fromparent: '',
    },
    methods: {
      refreshPage() {

      },
      doClick() {
        this.$root.showVM.$emit('isshow', this.show1);
        this.$router.push({name: 'kindsgoods', params: {code: JSON.stringify(this.goods)}})
        console.log(this.goods)
      }
    },
    created() {
      Vue.axios.get('/goods/getall').then((data)=> {
        this.allgoods = data.data;
        this.goods = [];
        for (let i = 0; i < this.allgoods.length; i++) {
          if (this.fromparent === this.allgoods[i].kind1) {
            this.goods.push(this.allgoods[i]);
          }
        }
      })

    },
    watch: {
      fromparent: function (v1, v2) {
        this.goods = [];
        for (let i = 0; i < this.allgoods.length; i++) {
          if (v1 === this.allgoods[i].kind1) {
            this.goods.push(this.allgoods[i]);
          }
        }
      }
    }
  }
</script>
<style scoped>
  .bigPic {
    background: url("../../assets/img/2分类/mianbuhuli.png") center no-repeat;
    background-size: 100% 100%;
    width: 15.6rem;
    height: 6.25rem;
    margin: 0.6rem 0.6rem 1.5rem;
  }
  .router {
    display: block;
    width: 100%;
    height: 100%;
  }
  .goods {
    width: 15.6rem;
    margin: 0.6rem;
    overflow: hidden;
  }
  .goods>p {
    font-size: 1rem;
    margin-bottom: 1.5rem;
  }
  .goods>div {
    width: 4rem;
    height: 4.8rem;
    float: left;
    text-align: center;
    margin-bottom: 1.2rem;
    margin-left: 0.45rem;
    margin-right: 0.45rem;
  }
  .goods>div>img {
    width: 4rem;
    height: 3rem;
  }
  .goods>div>p {
    font-size: 0.75rem;
  }
</style>



