       <h3>热销类目</h3>
       <ul class="flex">
         <li><img src="../../assets/img/1首页/3美食/tangguo.png" alt=""><div>糖果</div></li>
         <li><img src="../../assets/img/1首页/3美食/penghua.png" alt=""><div>膨化</div></li>
         <li><img src="../../assets/img/1首页/3美食/fangbianmian.png" alt=""><div>方便面</div></li>
         <li><img src="../../assets/img/1首页/3美食/qiaokeli.png" alt=""><div>巧克力</div></li>
