   </div>
</template>

<script>
  import * as $ from 'jquery'
  import * as Swiper from 'swiper'

    export default {
      mounted() {
        new Swiper['default']('.swiper-container', {
          loop: true,
          autoplay:true,
          // 如果需要分页器
          pagination: {
            el: '.swiper-pagination',
          },

          // 如果需要滚动条
          scrollbar: {
            el: '.swiper-scrollbar',
          },
        })
      }
    }
</script>

<style scoped>
  @import "../../../node_modules/swiper/dist/css/swiper.css";
  .swiper-slide > img {
    width: 100%;
  }
  .wrapper{
    width: 100%;
  }
  .wrap{
    width: 100%;
  }
  .class{
    width: 100%;
    text-align: center;
  }
  .tiao{
    width: 100%;
    height: 1.5rem;
    background: gainsboro;
    margin-top: -0.5rem;
    text-align: center;
    color: grey;
  }
  .flex{
    display: flex;
  }
  .flex li>img{
    height: 3rem;
  }
  .flex li{
    width: 4rem;
    height: 5rem;
    margin: 2rem 1rem 1rem;
  }
  .tuijian{
    text-align: center;
  }
  .baopin>img{
    height: 9rem;
    margin: 1rem;
  }
</style>



