<template>
  <div class="wrapper">
    <div class="header">
      <router-link :to="{path: '/classify'}" class="router"><img src="../../assets/img/2分类/返回.png" alt="" @click="sendToFooter"></router-link>
      <span>商品分类</span>
    </div>
    <div class="option">
      <div @click="doClick(option),PriceSort('price', isSort, index)" v-for="(option,index) in options" :class="{'active': activeOpt === option}" >{{option}}</div>
    </div>
    <div class="chooseOK" v-if="isShow">
      <div><div v-for="c in chooseArr" @click="deletekind(c)">{{c}}</div></div>
      <p @click="deleteAllKinds">清空</p>
    </div>
    <div class="content">
      <div v-for="g in goods">
        <div class="left">
          <img :src="g.img" alt="">
        </div>
        <div class="right">
          <p>{{g.name}}</p>
          <p>{{g.name}}{{g.name}}</p>
          <div class="info">
            <img :src="g.countrypic" alt="">
            <img src="../../assets/img/2分类/feiji.jpg" alt="">
            <span>海外直邮</span>
            <span>¥ {{g.price}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom"><img src="../../assets/img/2分类/datu.jpg" alt=""></div>
    <div class="filtrate" :class="{'active2': activeOpt === '筛选'}">
      <div class="top">
        <div class="place">
          <img src="../../assets/img/2分类/fahuodi.jpg" alt=""><span>发货地</span>
          <ul>
            <li v-for="p in place" @click="doChooseP(p)" :class="{'active3': activePlace === p}">{{p}}</li>
          </ul>
        </div>
        <div class="type">
          <img src="../../assets/img/2分类/shangpinleixing.jpg" alt=""><span>商品类型</span>
          <ul>
            <li v-for="t in type" @click="doChooseT(t)" :class="{'active3': activeType === t}">{{t}}</li>
          </ul>
        </div>
      </div>
      <div class="minuteKinds">
        <img src="../../assets/img/2分类/fenlei1.jpg" alt=""><span>分类</span>
        <ul class="kul">
          <li v-for="(k, index) in kindgoods" @click="doChooseK(k, index)" :class="{'active3': k.isActive === true}">{{k.kind1}}</li>
          <div @click="showmore">更多分类 ></div>
        </ul>
      </div>
      <div class="reset" @click="doreset">重置</div>
      <div class="sure" @click="dosure">确认</div>
    </div>
    <div class="more" :class="{'active2': activeMore === '更多'}">
      <div class="moreHeader">
        <img src="../../assets/img/2分类/返回2.png" alt="" @click="dohidemore">
        <span>分类</span>
        <span @click="dosure">确定</span>
      </div>
      <div class="moreKinds">
        <div v-for="(m, index) in kindgoods" @click="chooseMoreKind(m)" :class="{'active5': m.isActive}">{{m.kind1}}<img src="../../assets/img/2分类/duihao2.jpg" alt="" :class="{'active2': m.isActive}" class="sign"></div>
      </div>
    </div>
    <div class="mask" :class="{'active2': activeMask === '出现' || activeOpt === '筛选'}" @click="dohide"></div>
  </div>
</template>
<script>
  import * as $ from 'jquery'
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'
  export default {
    data() {
      return {
        options: ['销量', '最新', '价格', '筛选'],
        place: ['日本', '美国', '中国', '澳大利亚', '英国'],
        type: ['促销商品', '首发商品'],
        allgoods: [],
        kind: [],
        kindgoods: [],
        goods: [],
        goods1: [],
        chooseArr: [],
        activeOpt: '',
        activePlace: '',
        activeType: '',
        activeKind: '',
        activeMore: '',
        activeMask: '',
        activeMoreKind: '',
        show1: 1,
        isShow: false,
        isSort: true
      }
    },
    computed: {
//      Obj() {
//        this.goods =  JSON.parse(this.$route.params.code)
//        return this.goods
//      }
    },
    created() {
      Vue.axios.get('/goods/getall').then((data)=> {
        this.allgoods = data.data;
      })
      this.goods =  JSON.parse(this.$route.params.code);
      let kinds1 = [];
      let kinds2 = [];
      for (let i = 0; i < this.goods.length; i++) {
        kinds1.push(this.goods[i].kind1);
        kinds2.push(this.goods[i])
      }
      for(let i = 0; i < kinds1.length;i++){
        if(this.kind.indexOf(kinds1[i]) === -1){
          this.kind.push(kinds1[i]);
        }
      }
      for(let i = 0; i < kinds2.length;i++){
        if(this.kindgoods.indexOf(kinds2[i]) === -1){
          this.kindgoods.push(kinds2[i]);
        }
      }
    },
    methods: {
      // 选中状态
      doClick(option) {
        this.activeOpt = option;
        this.activePlace = this.goods[0].country;
      },
      // 选中状态
      doChooseP(e) {
        this.activePlace = e;
        this.kind = [];
        this.kindgoods = [];
        let kinds1 = [];
        let kinds2 = [];
        for (let i = 0; i < this.allgoods.length; i++) {
          if (this.allgoods[i].country === e) {
            kinds1.push(this.allgoods[i].kind1);
            kinds2.push(this.allgoods[i])
          }
        }
        for(let i = 0; i < kinds1.length;i++){
          if(this.kind.indexOf(kinds1[i]) === -1){
            this.kind.push(kinds1[i]);
          }
        }
        for(let i = 0; i < kinds2.length;i++){
          if(this.kindgoods.indexOf(kinds2[i]) === -1){
            this.kindgoods.push(kinds2[i]);
          }
        }
      },
      doChooseT(e) {
        this.activeType = e;
      },
      doChooseK(e, index) {
        e.isActive = !e.isActive;
        if (e.isActive === true) {
          this.chooseArr.push(e.kind1);
          for (let i = 0; i < this.allgoods.length; i++) {
            if (this.allgoods[i].kind1 === e.kind1) {
              this.goods.push(this.allgoods[i]);
            }
          }
        } else {
          for (let i = 0; i < this.chooseArr.length; i++) {
            if (e.kind1 === this.chooseArr[i]) {
              this.chooseArr.splice(i, 1);
            }
          }
          for (let i = 0; i < this.allgoods.length; i++) {
            if (this.allgoods[i].kind1 === e.kind1) {
              this.goods.splice(i, 1);
            }
          }
        }
        if (this.chooseArr.length > 0){
          this.isShow = true;
        } else {
          this.isShow = false;
        }
      },
      // 重置
      doreset() {
        this.activePlace = null;
        this.activeType = null;
        for (let i = 0; i < this.kind.length; i++) {
          this.kind[i].isActive = false;
        }
        this.chooseArr.splice(0, this.chooseArr.length);
        this.isShow = false;
      },
      // 使Footer隐藏
      sendToFooter() {
        this.$root.showVM.$emit('isshow', this.show1);
      },
      // 隐藏全部
      dohide() {
        this.activeOpt = null;
        this.activeMore = null;
        this.activeMask = null;
      },
      // 隐藏more菜单
      dohidemore() {
        this.activeMore = null;
        this.activeOpt = '筛选';
      },
      // 显示more菜单
      showmore() {
        this.activeMore = '更多';
        this.activeMask = '出现';
        this.activeOpt = null;
      },
      // 设置选中状态
      chooseMoreKind(m, index) {
        m.isActive = !m.isActive;
        this.goods = [];
        if (m.isActive === true) {
          this.chooseArr.push(m.kind1);
          for (let i = 0; i < this.allgoods.length; i++) {
            if (this.allgoods[i].kind1 === e.kind1) {
              this.goods.push(this.allgoods[i]);
            }
          }
        } else {
          for (let i = 0; i < this.chooseArr.length; i++) {
            if (m.kind1 === this.chooseArr[i]) {
              this.chooseArr.splice(i, 1);
            }
          }
          for (let i = 0; i < this.allgoods.length; i++) {
            if (this.allgoods[i].kind1 === m.kind1) {
              this.goods.splice(i, 1);
            }
          }
        }
        if (this.chooseArr.length > 0){
          this.isShow = true;
        } else {
          this.isShow = false;
        }
      },
      // 点击more中的确定
      dosure() {
        this.activeOpt = null;
        this.activeMore = null;
        this.activeMask = null;
      },
      // 删除某个标签
      deletekind(c) {
        this.goods1 = [];
        let cindex = this.chooseArr.indexOf(c);
        // 去掉菜单中的样式
        for (let i = 0; i < this.kindgoods.length; i++) {
          if (this.chooseArr[cindex] === this.kindgoods[i].kind1) {
            this.kindgoods[i].isActive = false;
          }
        }
        for (let i = 0; i < this.goods.length; i++) {
          this.goods1.push(this.goods[i].kind1);
          if (this.goods1[i] === c) {
            this.goods.splice(i, 1);
          }
        }
        // 数组中删除
        if (cindex > -1) {
          this.chooseArr.splice(cindex, 1);
        }
        if (this.chooseArr.length > 0){
          this.isShow = true;
        } else {
          this.isShow = false;
        }
      },
      deleteAllKinds(e) {
        for (let i = 0; i < this.kind.length; i++) {
          this.kind[i].isActive = false;
        }
        this.chooseArr.splice(0, this.chooseArr.length);
        this.isShow = false;
      },
      // 封装排序函数
      sortExp(key, isSort){
        return function(x, y){
          return (x[key] - y[key] * (isSort ? 1 : -1));
        }
      },
      // 进行排序
      PriceSort(key, isSort, index){
        if (index === 2){
          this.isSort = !this.isSort;
          this.goods.sort(this.sortExp(key, isSort));
          for (let i = 0; i < this.goods.length; i++) {
            console.log(this.goods[i].name)
          }
        }
        if (index === 3) {
          this.goods = [];
        }
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .option {
    margin-top: 3rem;
    height: 1rem;
    padding: 0.75rem 2rem;
    display: flex;
    justify-content: space-between;
  }
  .option>div {
    display: inline-block;
  }
  .content {
    overflow: scroll;
    position: relative;
    width: 100%;
  }
  .content>div {
    width: 100%;
    height: 5rem;
    padding: 0.75rem;
    overflow: hidden;
    background-color: #ffffff;
    margin-bottom: 1rem;
  }
  .left {
    width: 5rem;
    height: 5rem;
    float: left;
    text-align: center;
  }
  .left>img {
    height: 5rem;
  }
  .right {
    height: 5rem;
    float: left;
    margin-left: 1rem;
  }
  .right>p:nth-child(1) {
    font-size: 1rem;
  }
  .right>p:nth-child(2) {
    width: 15rem;
    font-size: 0.75rem;
  }
  .info {
    height: 1.5rem;
    margin-top: 1rem;
    vertical-align: top;
  }
  .info>img:nth-child(1) {
    width: 1.5rem;
    height: 1rem;
    float: left;
    margin-right: 1rem;
  }
  .info>img:nth-child(2) {
    width: 1rem;
    height: 1rem;
  }
  .info>span:nth-child(3) {
    font-size: 0.75rem;
    vertical-align: top;
  }
  .info>span:nth-child(4) {
    font-size: 1rem;
    color: #e53e42;
    float: right;
  }
  .bottom {
    width: 100%;
  }
  .bottom>img {
    width: 100%;
  }
  .filtrate {
    width: 80%;
    position: absolute;
    background: #ffffff;
    z-index: 500;
    top: -3rem;
    right: 0;
    padding: 0.6rem 0 0.6rem 0.8rem;
    display: none;
  }
  .place {
    width: 100%;
    height: 8.25rem;
  }
  .type {
    width: 100%;
    height: 5rem;
    border-bottom: 1px solid #cccccc;
  }
  .minuteKinds {
    margin-top: 0.6rem;
  }
  .top>div>img, .minuteKinds>img {
    width: 1rem;
    height: 1rem;
    vertical-align: middle;
  }
  .top>div>span, .minuteKinds>span {
    font-size: 1rem;
    color: #4d4d4d;
    margin-left: 0.5rem;
    vertical-align: middle;
  }
  li {
    width: 5rem;
    height: 0.9rem;
    background-color: #f2f2f2;
    border-radius: 0.31rem;
    text-align: center;
    font-size: 0.9rem;
    padding: 0.8rem 0;
    margin: 0.3rem;
    float: left;
  }
  .minuteKinds>ul>div:last-child {
    background-color: #ffffff;
    float: right;
    margin-right: 1.5rem;
  }
  .reset, .sure {
    width: 50%;
    position: absolute;
    text-align: center;
    font-size: 1rem;
    padding: 1rem 0;
    bottom: -3rem;
  }
  .reset {
    left: 0;
    background-color: #f2f2f2;
  }
  .sure {
    color: #ffffff;
    right: 0;
    background-color: #e53e42;
  }
  .more {
    position: absolute;
    background-color: #ffffff;
    width: 80%;
    top: -3rem;
    right: 0;
    z-index: 600;
    display: none;
  }
  .moreHeader {
    width: 100%;
    height: 1.5rem;
    background: #f2f2f2;
    position: absolute;
    padding: 0.75rem 0;
    top: 0;
    text-align: center;
  }
  .moreHeader>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .moreHeader>span:nth-child(2) {
    font-size: 1.4rem;
    vertical-align: bottom;
  }
  .moreHeader>span:nth-child(3) {
    font-size: 1.2rem;
    position: absolute;
    right: 0.5rem;
    top: 1rem;
  }
  .moreKinds {
    margin-top: 3rem;
  }
  .moreKinds>div {
    font-size: 1rem;
    padding: 0.75rem 0.6rem;
    border-bottom: 1px solid #cccccc;
  }
  .sign {
    float: right;
    width: 1rem;
    display: none;
  }
  .mask {
    position: absolute;
    top: -3rem;
    bottom: 0;
    width: 100%;
    background-color: black;
    opacity: 0.4;
    z-index: 450;
    display: none;
  }
  .chooseOK {
    width: 100%;
    height: 2rem;
    padding: 1rem 0;
    background-color: #ffffff;
    border-bottom: 1px solid #cccccc;
  }
  .chooseOK>div {
    display: inline-block;
    width: 60%;
    height: 4rem;
    overflow: hidden;
  }
  .chooseOK>div>div {
    display: inline-block;
    font-size: 1rem;
    padding: 0.5rem;
    color: #ffffff;
    background-color: #999999;
    margin: 0 0.5rem 1rem;
    border-radius: 0.5rem;
  }
  .chooseOK>p {
    border: 1px solid #999999;
    border-radius: 0.5rem;
    margin: 0 1rem;
    font-size: 1rem;
    padding: 0.5rem;
    float: right;
  }
  .active {
    color: red;
  }
  .active2 {
    display: block;
  }
  .active3 {
    background: url("../../assets/img/2分类/duihao.jpg") no-repeat center;
    background-size: 100% 100%;
  }
  .active4 {
    background: url("../../assets/img/2分类/duihao.jpg") no-repeat center;
    background-size: 100% 100%;
  }
  .active5 {
    color: red;
  }
</style>



