<template>
  <div class="goodscontent">
    <div class="goodsID">
      <span>商品编号: 1485269676465</span>
      <span>已发货</span>
    </div>
    <ul class="goodsInfo">
      <li @click="toOrderDetails">
        <div class="left">
          <img src="../../../assets/img/4购物车/mifen.png" alt="">
        </div>
        <div class="center">
          <p>会呼吸丝滑蜜粉</p>
          <p>规格: 蜜粉  综合税0.2%</p>
          <p>¥ 166.06 <span>X 1件</span></p>
        </div>
      </li>
    </ul>
    <div class="total">
      <p><span>实付:</span><span>¥ 166.06</span></p><p><span>确认收货</span></p>
    </div>
  </div>
</template>
<script>
  export default {
    methods: {
      toOrderDetails() {
        this.$router.push({name: 'orderdetails'});
      }
    }
  }
</script>
<style scoped>
  .goodscontent {
    width: 100%;
    overflow: scroll;
    background-color: #ffffff;
  }
  .goodsID {
    overflow: hidden;
    padding: 0.5rem 0.6rem;
    border-bottom: 1px solid #cccccc;
  }
  .goodsID>span:nth-child(1) {
    float: left;
  }
  .goodsID>span:nth-child(2) {
    float: right;
    color: red;
  }
  .goodsInfo {
    width: 100%;
    background-color: #ffffff;
  }
  .goodsInfo>li {
    border-bottom: 1px solid #cccccc;
    height: 6rem;
  }
  .left {
    width: 6rem;
    height: 100%;
    float: left;
    vertical-align: middle;
    padding-left: 0.6rem;
  }
  .left>img {
    width: 3rem;
    height: 3rem;
    margin-top: 1.5rem;
    margin-left: 1.5rem;
  }
  .center {
    float: left;
    height: 100%;
    padding: 0.6rem 0 0 1.5rem;
  }
  .center>p:nth-child(1) {
    margin-top: 0.4rem;
    font-size: 1rem;
  }
  .center>p:nth-child(2) {
    margin-top: 0.3rem;
    font-size: 0.75rem;
    color: #666666;
  }
  .center>p:nth-child(3) {
    margin-top: 0.6rem;
    font-size: 0.75rem;
    color: red;
  }
  .center>P>span {
    color: #808080;
    margin-left: 1rem;
  }
  .total {
    padding: 0.6rem 0;
    width: 100%;
    overflow: hidden;
    vertical-align: middle;
  }
  .total>p:nth-child(1) {
    display: inline-block;
    float: left;
    margin-left: 0.6rem;
    margin-top: 0.1rem;
  }
  .total>p:nth-child(2) {
    display: inline-block;
    float: right;
    margin-right: 0.6rem;
  }
  .total>p:nth-child(1)>span:nth-child(1) {
    float: left;
  }
  .total>p:nth-child(1)>span:nth-child(2) {
    color: #e53e42;
  }
  .total>p:nth-child(2)>span {
    color: #ffffff;
    width: 5rem;
    font-size: 1rem;
    line-height: 1.5rem;
    background-color: #e53e42;
    text-align: center;
    border-radius: 0.6rem;
    display: inline-block;
  }
</style>



