<template>
  <div class="warpper">
    <div class="header">
      <a class="router" @click="goBack"><img src="../../../assets/img/2分类/返回.png" alt="" ></a>
      <span>修改手机号</span>
      <span>保存</span>
    </div>
    <div class="content">
      <input type="text" placeholder="15012340809">
    </div>
  </div>
</template>
<script>
  export default {
    methods: {
      goBack() {
        window.history.go(-1);
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span:nth-child(2) {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .header>span:nth-child(3) {
    font-size: 1rem;
    color: #ffffff;
    vertical-align: middle;
    position: absolute;
    right: 0.6rem;
    top: 1.2rem;
  }
  .content {
    width: 100%;
    overflow: scroll;
    margin-top: 3rem;
  }
  input {
    width: 100%;
    padding: 0.6rem;
    appearance: none;
    border: none;
    outline: none;
    margin-top: 1rem;
  }
</style>



