<template>
    <div class="body">
      <!--页眉-->
      <div class="top">
        <img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack">
        <span>客服中心</span>
        <a>人工服务</a>
      </div>
      <div class="chat">
        <div class="center">14:30</div>
        <div class="center">机器人为您服务</div>
        <div ><img src="../../assets/img/5个人中心/客服与反馈/客服中心/1.png" alt="" class="chat1"></div>
      </div>
      <div  v-for="obj in news">
        <div class="chatX">
          <span>{{obj}}</span><img src="../../assets/img/5个人中心/客服与反馈/客服中心/mypic.png" alt="" >
        </div>
        <div class="chatY">
          <img src="../../assets/img/5个人中心/客服与反馈/客服中心/kefupic.png" alt=""><span>您说的太特么对了!</span>
        </div>
      </div>
      <div class="bottom">
       <input style="border:0; border-bottom:1px solid skyblue; background:#f2f2f2 ; outline:none" v-model="data"><span @click="doSend">发送</span>
      </div>
    </div>
</template>

<script>
    export default {
      data() {
        return{
          data:'',
          news:[]
        }
      },
      methods: {
        doBack() {
          this.$router.go(-1);
        },
        doSend() {
          if(this.data !== ''){
            this.news.push(this.data);
          }
          this.data = '';
        }
      }
    }
</script>

<style scoped>
  body{
    background: #f2f2f2;
  }
  .body {
    background:  #f2f2f2;
    width: 100%;
    /*height: 50rem;*/
    overflow: scroll;
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 300;
  }

  .top {
    width: 100%;
    height: 3rem;
    background: red;
    color: white;
    font-size: 1.5rem;
  }

  .top img {
    height: 1.5rem;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
  }

  .top span {
    margin-left: 6rem;
    margin-right: 1.5rem;
  }
  .center{
    margin-top: 0.5rem;
   text-align: center;
  }
  .chat{
    width: 100%;
    background: #f2f2f2;
  }
  .chat1{
    height: 8rem;
    margin-left: 0.5rem;
  }
  .bottom{
    text-align: center;
    width: 100%;
    position: fixed;
    bottom: 0;
  }
  .chatX{
    margin-right: 0.5rem;
    text-align: right;
  }
  .chatX img{
    height: 3rem;
    vertical-align: middle;
  }
  .chatY img{
    height: 3rem;
    vertical-align: middle;
  }
</style>



