import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '../components/HomePage/home-page'
import TimeOutGoods from '../components/HomePage/TimeOutGoods'
import BrandSelected from '../components/HomePage/BrandSelected'
import HotSale from '../components/HomePage/HotSale'
import Gorgeous from '../components/HomePage/Gorgeous'
import TimeOut from '../components/HomePage/TimeOut'
import LunBo from '../components/HomePage/lunbo'
import Searchtxt from '../components/MyHeader/Searchtxt'
import MiJi from '../components/HomePage/MiJi'
import Mijicomt from '../components/HomePage/Mijicomt'
import Classify from '../components/Classify/Classify'
import StockOut from '../components/StockOut/StockOut'
import ShopCarts from '../components/ShopCarts/ShopCarts'
import Personal from '../components/MyPersonal/Personal.vue'
import KindsGoods from '../components/result/KindsGoods.vue'
import YRecommend from '../components/HomePage/y-Recommend'
import YMiss from '../components/HomePage/y-Miss'
import YMomAndBaby from '../components/HomePage/y-MomAndBaby'
import YNiceFood from '../components/HomePage/y-NiceFood'
import XRecommend from '../components/Classify/XRecommend'
import XKinds from '../components/Classify/XKinds.vue'
import Product from '../components/StockOut/Product'
import Order from '../components/ShopCarts/order.vue'
import Gm from '../components/MyPersonal/gm.vue'
import Public from '../components/MyPersonal/Public.vue'
import CountCenter from '../components/ShopCarts/CountCenter.vue'
import Brand from '../components/StockOut/Brand'
import Evaluate from '../components/StockOut/Evaluate'
import EvaluateAll from '../components/StockOut/EvaluateAll'
import EvaluateDetails from '../components/StockOut/EvaluateDetails'
import EvaluatePics from '../components/StockOut/EvaluatePics'
import Address from '../components/MyPersonal/Address.vue'
import NewAddress from '../components/MyPersonal/NewAddress.vue'
import Search from '../components/MyHeader/Search'
import News from '../components/MyHeader/News'
import NewGoods from '../components/HomePage/NewGoods'
import AllOrder from '../components/MyPersonal/AllOrders/AllOrder.vue'
import AllOrderAll from '../components/MyPersonal/AllOrders/AllOrderAll.vue'
import AllOrderPay from '../components/MyPersonal/AllOrders/AllOrderPay.vue'
import AllOrderPost from '../components/MyPersonal/AllOrders/AllOrderPost.vue'
import AllOrderGet from '../components/MyPersonal/AllOrders/AllOrderGet.vue'
import AllOrderComplete from '../components/MyPersonal/AllOrders/AllOrderComplete.vue'
import Option from '../components/MyPersonal/Options/Option.vue'
import OrderDetails from '../components/MyPersonal/AllOrders/OrderDetails.vue'
import AccountOption from '../components/MyPersonal/Options/AccountOption.vue'
import AccountInfo from '../components/MyPersonal/Options/AccountInfo.vue'
import NickName from '../components/MyPersonal/Options/NickName.vue'
import Phone from '../components/MyPersonal/Options/Phone.vue'
import CusServer from '../components/MyPersonal/Customerservice'
import Passwords from '../components/MyPersonal/Options/Passwords.vue'
import Register from '../components/MyPersonal/Register.vue'
import PersonalInfo from '../components/MyPersonal/PersonalInfo/PersonalInfo.vue'
import ChangeImage from '../components/MyPersonal/PersonalInfo/ChangeImage.vue'
import QRCode from '../components/MyPersonal/PersonalInfo/QRCode.vue'
import InviteCode from '../components/MyPersonal/PersonalInfo/InviteCode.vue'
import RealName from '../components/MyPersonal/RealName.vue'
import AddStatus from '../components/MyPersonal/AddStatus.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: 'homepage'
    },
    {
      path: '/homepage',
      component: HomePage,
      redirect: '/homepage/recommend',
      name: 'homepage',
      children: [
        {path: 'recommend', name: 'homepage/recommend', component: YRecommend},
        {path: 'miss', name: 'homepage/miss', component: YMiss},
        {path: 'nicefood', name: 'homepage/nicefood', component: YNiceFood},
        {path: 'momandbaby', name: 'homepage/momandbaby', component: YMomAndBaby}
      ]
    },
    {path: '/classify', component: Classify, redirect: '/classify/recommend',
      name: 'classify',
      children: [
        {path: 'recommend', name: 'classify/recommend', component: XRecommend},
        {path: 'kinds', name: 'classify/kinds', component: XKinds}
      ]
    },
    {path: '/stockout', name: 'stockout', component: StockOut},
    {path: '/product/:name', name: 'product', meta: { keepAlive: true }, component: Product},
    {
      path: '/evaluate',
      component: Evaluate,
      name: 'evaluate',
      redirect:'/evaluate/evaluateall',
      children: [
        {path: 'evaluateall/:name', component: EvaluateAll, name: 'evaluate/evaluateall'},
        {path: 'evaluatedetails/:comment', component: EvaluateDetails, name: 'evaluate/evaluatedetails'},
        {path: 'evaluatepics', component: EvaluatePics, name: 'evaluate/evaluatepics'}
      ]
    },
    {path: '/brand', component: Brand, name: 'brand'},
    {path: '/shopcarts', name: 'shopcarts', component: ShopCarts},
    {path: '/personal', component: Personal, name: 'personal'},
    {path: '/kindsgoods/:code', component: KindsGoods, name: 'kindsgoods'},
    {path: '/gm', component: Gm, name: 'gm'},
    {path:'/public', component:Public, name: 'public'},
    {path: '/order', component: Order, name: 'order'},
    {path: '/countcenter', component: CountCenter, name: 'countcenter'},
    {path: '/address', component: Address, name: 'address'},
    {path: '/newaddress', component: NewAddress, name: 'newaddress'},
    {path: '/search',component: Search, name: 'search'},
    {path: '/searchtxt/:name',component: Searchtxt, name: 'searchtxt'},
    {path: '/news', component: News, name: 'news'},
    {path: '/timeout', component: TimeOut, name: 'TimeOut'},
    {path: '/mijicomt', component: Mijicomt, name: 'mijicomt'},
    {path: '/miji', component: MiJi, name: 'miji'},
    {path: '/lunbo', component: LunBo, name: 'lunbo'},
    {path: '/timeoutgoods', component: TimeOutGoods, name: 'timeoutgoods'},
    {path: '/cusserver', component: CusServer, name: 'cusserver'},
    {path: '/brandselected', component: BrandSelected, name: 'brandselected'},
    {path: '/gorgeous', component: Gorgeous, name: 'gorgeous'},
    {path: '/hotsale', component: HotSale, name: 'hotsale'},
    {path: '/newgoods',component: NewGoods, name: 'newgoods'},
    {
      path: '/allorder', component: AllOrder, name: 'allorder', redirect: '/allorder/all',
      children: [
        {path: 'all', component: AllOrderAll, name: 'allorder/all'},
        {path: 'pay', component: AllOrderPay, name: 'allorder/pay'},
        {path: 'post', component: AllOrderPost, name: 'allorder/post'},
        {path: 'get', component: AllOrderGet, name: 'allorder/get'},
        {path: 'complete', component: AllOrderComplete, name: 'allorder/complete'},
      ]
    },
    {path: '/option', component: Option, name: 'option'},
    {path: '/orderdetails', component: OrderDetails, name: 'orderdetails'},
    {path: '/accountoption', component: AccountOption, name: 'accountoption'},
    {path: '/accountinfo', component: AccountInfo, name: 'accountinfo'},
    {path: '/nickname', component: NickName, name: 'nickname'},
    {path: '/phone', component: Phone, name: 'phone'},
    {path: '/passwords', component: Passwords, name: 'passwords'},
    {path: '/register', component: Register, name: 'register'},
    {path: '/personalinfo', component: PersonalInfo, name: 'personalinfo'},
    {path: '/changeimage', component: ChangeImage, name: 'changeimage'},
    {path: '/qrcode', component: QRCode, name: 'qrcode'},
    {path: 'invitecode', component: InviteCode, name: 'invitecode'},
    {path: '/realname', component: RealName, name: 'realname'},
    {path: '/addstatus', component: AddStatus, name: 'addstatus'}
  ]
})



