<template class="temp">
  <div>
  <div class="body">
    <!--页眉-->
    <div class="top">
      <img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack">
      <span>阿拉粉心得</span>
    </div>
    <!--分栏-->
    <div>
      <ul class="flex">
        <li @click="toEvaluateall">全部心得</li>
        <li @click="toEvaluatepics">晒图</li>
      </ul>
    </div>
  </div>
    <router-view></router-view>
  </div>
</template>
<script>
  export default {
    methods: {
      doBack() {
        this.$router.go(-1);
      },
      toEvaluateall() {
        this.$router.push({name: 'evaluate/evaluateall'});
      },
      toEvaluatepics() {
        this.$router.push({name: 'evaluate/evaluatepics'});
      }
    }
  }
</script>

<style scoped>


  .body {
    background: white;
    width: 100%;
    overflow: scroll;
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 300;
  }
  .top {
    width: 100%;
    height: 3rem;
    background: red;
    color: white;
    font-size: 1.5rem;
  }
  .top img {
    height: 1.5rem;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
  }

  .top span {
    margin-left: 7rem;
    margin-right: 5.5rem;
  }
  .flex{
    display: flex;
    border-bottom: 1px solid gainsboro;
    padding: 0.5rem;
  }
  .flex li{
    text-align: center;
    flex: 1;
    font-size: 1.2rem;
  }
  .flex li:hover{
    color: red;
  }
  .flex li:nth-child(1){
    border-right: 2px solid grey;
  }
</style>



