<template>
  <div class="body">
    <!--页眉-->
    <div class="top">
      <img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack">
      <span>阿拉粉心得</span>
      <img src="../../assets/img/3断货王/断货王/share.png" alt="">
    </div>
    <div class="persen">
      <img :src="comment[0].pic" alt="">
      <span class="nic">{{comment[0].nickname}}</span>
    </div>
    <div class="txt">
      {{comment[0].txt}}
    </div>
    <div class="pic">
      <img :src="comment[0].goodimg" alt="">
    </div>
    <div class="time">
      {{comment[0].time}}
    </div>
    <!--商品-->
    <div class="good">
      <ul class="flax">
        <li class="li1"><img :src="comment[0].goodimg" alt=""></li>
        <li class="li2">
          <div>{{comment[0].goodname}}</div>
          <div>海外直邮</div>
          <div>￥166.06</div>
        </li>
      </ul>
    </div>
    <div >
      <div class="yonghu">
        <h3>用户评论({{comment[0].comment.length}})</h3>
      </div>
      <div v-for="obj in comment[0].comment">
      <div class="persen">
        <img :src="obj.pic" alt="">
        <span class="nic">{{obj.nickname}} <br>{{obj.time}} </span>
      </div>
      <div class="txt">
       {{obj.txt}}
      </div>
      </div>
      <div class="fenlan">
        <ul class="flex">
          <li><img src="../../assets/img/3断货王/全部心得/zan.png" alt="">{{comment[0].nice}}</li>
          <li><img src="../../assets/img/3断货王/全部心得/comment.png" alt="">{{comment[0].comment.length}}</li>
          <li><img src="../../assets/img/3断货王/全部心得/share.png" alt=""></li>
        </ul>
      </div>
      <div class="tiao"></div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        comment: [],
        pic:'',
        txt:''
      }
    },
    methods: {
      doBack() {
        this.$router.go(-1);
      }
    },
    created() {
      this.comment = this.$route.params.comment;
    }
  }
</script>

<style scoped>
  .yonghu {
    height: 3rem;
    line-height: 3rem;
    border: 1px solid gainsboro;
    margin-top: 1rem;
    padding-left: 1rem;
  }

  .flax {
    display: flex;
  }

  .li1 {
    flex: 1;
    border-right: gainsboro 1px solid;
    padding: 1rem;
  }

  .li1 img {
    height: 5rem;
  }

  .li2 {
    flex: 3;
  }

  .li2 div {
    margin-left: 0.5rem;
    margin-bottom: 1rem;
  }

  .li2 div:nth-child(3) {
    color: red;
  }

  .time {
    padding: 1rem;
    color: #808080;
  }

  .tiao {
    width: 100%;
    height: 1.5rem;
    background: gainsboro;
    text-align: center;
    opacity: 0.8;
  }

  .top {
    width: 100%;
    height: 3rem;
    background: red;
    color: white;
    font-size: 1.5rem;
  }

  .top img {
    height: 1.5rem;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
  }

  .top span {
    margin-left: 7rem;
    margin-right: 2.5rem;
  }

  .persen {
    padding: 1rem;
  }

  .nic {
    margin-right: 11rem;
  }

  .persen img {
    vertical-align: middle;
    margin-right: 0.5rem;
  }

  .txt {
    padding-left: 1rem;
    padding-right: 3rem;
    color: #808080;
  }

  .pic {
    padding-bottom: 1rem;
  }

  .pic img {
    height: 6rem;
    margin-left: 1rem;
    margin-top: 0.5rem;
    border: 1px solid gainsboro;
  }

  .flex {
    display: flex;
    padding: 0.5rem;
  }

  .flex img {
    height: 1rem;
    vertical-align: middle;
  }

  .flex li {
    flex: 1;
    text-align: center;
    color: #808080;
  }

  .flex li:nth-child(2) {
    border-right: 2px solid gainsboro;
    border-left: 2px solid gainsboro;
  }

  .good {
    margin-left: 1rem;
    width: 22rem;
    height: 7rem;
    border: 1px gainsboro solid;
  }
</style>



