<template>
  <div class="wrapper">
    <div class="header">
      <span>购物车</span>
    </div>
    <div class="empty" v-if="isEmpty">
      <p>您的购物车还是空的,去<a href="">逛逛</a>吧!</p>
    </div>
    <div class="content">
      <div class="share">
        <img src="../../assets/img/4购物车/fenxiangbg.png" alt="">
        <img src="../../assets/img/4购物车/分享送豪礼.png" alt="">
        <img src="../../assets/img/4购物车/去分享.png" alt="">
      </div>
      <div class="kind">
        <img src="../../assets/img/4购物车/duihao.png" alt="">
        <img src="../../assets/img/4购物车/plane.png" alt="">
        <span>日本直邮</span>
      </div>
      <ul class="goods">
        <li v-for="(b, index) in buyGoods">
          <div class="left">
            <span class="DH" :class="{'active1': b.isActive}" @click="doChoose(b, index)"></span>
            <img :src="b.img" alt="">
          </div>
          <div class="center" @click="toProduct">
            <p>{{b.name}}</p>
            <p>规格: {{b.name}}</p>
            <p>¥ {{b.price}}</p>
          </div>
          <div class="right">
            <div>
              <div class="add" @click="minusCount(b, index)"></div>
              <div class="count">{{b.num}}</div>
              <div class="minus" @click="addCount(b, index)"></div>
            </div>
          </div>
        </li>
      </ul>
      <ul class="cost">
        <li><span>商品金额(不含税)</span><span>¥ {{sumPrice | currency('', 2)}}</span></li>
        <li><span>综合税总额</span><span>¥ {{0.3 | currency('', 2)}}</span></li>
        <li><span>应付总金额</span><span>¥ {{sumPrice - 0.3 | currency('', 2)}}</span></li>
        <li><router-link :to="{path: '/order'}" class="router"><span @click="sendToFooter">结算(1)</span></router-link></li>
      </ul>
      <ul class="lose" v-if="showLose">
        <div class="loseDiv">
          <img src="../../assets/img/4购物车/shixiao.png" alt="">
          <span>失效商品</span>
        </div>
        <li @click="toProduct">
          <div class="left">
            <img src="../../assets/img/4购物车/huiduihao.png" alt="">
            <img src="../../assets/img/4购物车/mifen.png" alt="">
          </div>
          <div class="center">
            <p>会呼吸丝滑蜜粉</p>
            <p>规格: 蜜粉  综合税0.2%</p>
            <p>¥ 166.06</p>
          </div>
        </li>
        <div class="clearLose">
          <div @click="clearLose">清除失效商品</div>
        </div>
      </ul>
      <div class="like">
        <p>猜你喜欢</p>
        <ul>
          <li v-for="g in goods" @click="toProduct">
            <img :src="g.img" alt="">
            <p>{{g.name}}</p>
            <p>¥ {{g.price}}</p>
          </li>
        </ul>
        <div class="more">加载更多</div>
        <div class="sorry">(亲, 已经到底了哦~)</div>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
  export default {
    data() {
      return {
        isEmpty: false,
        show1: 0,
        buyGoods: [],
        goods: [
          {
            name: '不易脱口红',
            img: require("../../assets/img/1首页/1推荐/kouhongjinse.png"),
            price: 59,
          },
          {
            name: '花蕊唇彩',
            img: require("../../assets/img/1首页/1推荐/hualeichuncai.png"),
            price: 129,
          },
          {
            name: '娇吻玉口红',
            img: require("../../assets/img/1首页/1推荐/jiaochuankouhong.png"),
            price: 88,
          },
          {
            name: '丰唇蜜唇彩',
            img: require("../../assets/img/4购物车/fengchunmi.png"),
            price: 99,
          },
          {
            name: '不易脱口红',
            img: require("../../assets/img/1首页/1推荐/kouhongjinse.png"),
            price: 59,
          },
          {
            name: '花蕊唇彩',
            img: require("../../assets/img/1首页/1推荐/hualeichuncai.png"),
            price: 129,
          },
          {
            name: '娇吻玉口红',
            img: require("../../assets/img/1首页/1推荐/jiaochuankouhong.png"),
            price: 88,
          },
          {
            name: '丰唇蜜唇彩',
            img: require("../../assets/img/4购物车/fengchunmi.png"),
            price: 99,
          }
        ],
        showLose: true,
        singPrice: 0,
        sumPrice: 0
      }
    },
    methods: {
      sendToFooter() {
        this.$root.showVM.$emit('isshow', this.show1);
      },
      toProduct() {
        this.$router.push({name: 'product'});
      },
      clearLose() {
        this.showLose = false;
      },
      doChoose(b, index) {
        b.isActive = !b.isActive;
        if (b.isActive === true) {
          this.sumPrice += b.num * b.price;
        } else {
          this.sumPrice -= b.num * b.price;
        }
      },
      addCount(b, index) {
        if (b.isActive === true) {
          this.sumPrice += b.price;
        }
        b.num++;
      },
      minusCount(b, index) {
        if (b.num > 1) {
          b.num--;
          if (b.isActive === true) {
            this.sumPrice -= b.price;
          }
        } else {
          b.num = 1;
        }
      }
    },
    created() {
      axios.post('/goods/getsCar').then((data) => {
        this.buyGoods = data.data
      });
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: scroll;
    background-color: #f2f2f2;
    padding-bottom: 4rem;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    text-align: center;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
  }
  .header>span {
    font-size: 1.5rem;
    color: #ffffff;
  }
  .empty {
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    background: #ffffff;
    z-index: 600;
  }
  .empty>p {
    margin-top: 80%;
    margin-left: 35%;
    width: 7rem;
  }
  .empty>p>a {
    color: red;
  }
  .content {
    width: 100%;
    margin-top: 3rem;
  }
  .share {
    position: relative;
    width: 100%;
    height: 8rem;
    position: relative;
  }
  .share>img:nth-child(1) {
    width: 100%;
  }
  .share>img:nth-child(2) {
    position: absolute;
    top: 2rem;
    right: 1rem;
    width: 7rem;
  }
  .share>img:nth-child(3) {
    width: 4rem;
    position: absolute;
    top: 3.5rem;
    right: 4rem;
  }
  .kind {
    width: 100%;
    height: 1rem;
    padding: 1rem 0.6rem;
    margin-top: -2rem;
    border-bottom: 1px solid #cccccc;
    background-color: #ffffff;
  }
  .kind>img {
    width: 1rem;
    height: 1rem;
  }
  .kind>span {
    font-size: 1rem;
  }
  .goods {
    width: 100%;
    background-color: #ffffff;
  }
  .goods>li {
    width: 100%;
    border-bottom: 1px solid #cccccc;
    height: 6rem;
  }
  .left {
    width: 6rem;
    height: 100%;
    float: left;
    vertical-align: middle;
    padding-left: 0.6rem;
  }
  .left>img:nth-child(2) {
    width: 3rem;
    height: 3rem;
    margin-top: 1.5rem;
    margin-left: 1.5rem;
  }
  .center {
    float: left;
    height: 100%;
    padding: 0 0 0 1.5rem;
  }
  .center>p:nth-child(1) {
    margin-top: 0.4rem;
    font-size: 1rem;
  }
  .center>p:nth-child(2) {
    margin-top: 0.3rem;
    font-size: 0.75rem;
    color: #666666;
  }
  .center>p:nth-child(3) {
    margin-top: 0.6rem;
    font-size: 0.75rem;
    color: red;
  }
  .right {
    float: right;
    padding-right: 0.6rem;
    width: 25%;
    height: 100%;
    position: relative;
  }
  .right>div {
    position: absolute;
    bottom: 0.6rem;
    right: 0.6rem;
    width: 5rem;
    height: 1.5rem;
    background: url("../../assets/img/4购物车/圆角矩形2.png") no-repeat center;
    background-size: 100% 100%;
  }
  .right>div>div {
    display: inline-block;
  }
  .add, .minus {
    width: 1.3rem;
    height: 1.3rem;
    position: absolute;
    top: 0.1rem;
  }
  .add {
    left: 0.1rem;
    background: url("../../assets/img/4购物车/减.png") no-repeat center;
    background-size: 100% 100%;
  }
  .minus {
    right: 0.1rem;
    background: url("../../assets/img/4购物车/加.png") no-repeat center;
    background-size: 100% 100%;
  }
  .count {
    width: 2rem;
    position: absolute;
    left: 1.5rem;
    text-align: center;
  }
  .cost {
    width: 100%;
    background-color: #ffffff;
  }
  .cost>li {
    width: 100%;
    height: 1rem;
    padding: 1rem 2rem;
    overflow: hidden;
    border-bottom: 1px solid #cccccc;
    position: relative;
  }
  .cost>li>span:nth-child(1) {
    font-size: 1rem;
    text-align: left;
    color: #4d4d4d;
    float: left;
  }
  .cost>li>span:nth-child(2) {
    font-size: 1rem;
    text-align: right;
    color: red;
    float: right;
    margin-right: 3rem;
  }
  .cost>li>.router {
    height: 1rem;
    padding: 0.5rem 1.5rem;
    background: #e53e42;
    color: white;
    border-radius: 0.5rem;
    position: absolute;
    right: 4.5rem;
    top: 0.5rem
  }
  .lose {
    width: 100%;
    background-color: #ffffff;
  }
  .loseDiv {
    width: 100%;
    height: 1rem;
    padding: 1rem 0.6rem;
    margin-top: 1rem;
    border-bottom: 1px solid #cccccc;
    border-top: 1px solid #cccccc;
  }
  .loseDiv>img {
    width: 1rem;
    height: 1rem;
  }
  .loseDiv>span {
    font-size: 1rem;
  }
  .lose>li {
    border-bottom: 1px solid #cccccc;
    height: 6rem;
  }
  .clearLose {
    width: 100%;
    text-align: center;
    height: 1.5rem;
    padding: 1rem 0;
    border-bottom: 1px solid #cccccc;
  }
  .clearLose>div {
    display: inline-block;
    font-size: 1rem;
    padding: 0 1rem;
    border: 1px solid #cccccc;
    border-radius: 0.5rem;
  }
  .like {
    width: 100%;
    background-color: #ffffff;
    margin-top: 1rem;
  }
  .like>p {
    width: 100%;
    text-align: center;
    font-size: 1rem;
    padding: 1rem 0;
  }
  .like>ul {
    width: 100%;
    overflow: hidden;
  }
  .like>ul>li {
    width: 21%;
    padding: 2%;
    text-align: center;
    float: left;
  }
  .like>ul>li>img {
    width: 2.5rem;
    height: 5rem;
  }
  .like>ul>li>p:nth-child(2) {
    font-size: 0.8rem;
  }
  .like>ul>li>p:nth-child(3) {
    font-size: 0.8rem;
    color: red;
  }
  .more {
    width: 100%;
    font-size: 0.56rem;
    color: #808080;
    text-align: center;
    background-color: #ffffff;
    position: absolute;
    /*display: none;*/
  }
  .sorry {
    font-size: 0.75rem;
    color: red;
    text-align: center;
  }
  .left>.DH {
    width: 1rem;
    height: 1rem;
    display: inline-block;
    background: url("../../assets/img/4购物车/huiduihao.png") center no-repeat;
    background-size: 100% 100%;
    margin-bottom: 1rem;
  }
  .left>.active1 {
    background: url("../../assets/img/4购物车/duihao.png") center no-repeat;
    background-size: 100% 100%;
  }
</style>



