<template>
<div class="body">
  <!--页眉-->
  <div class="top">
    <img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack">
    <span>绚丽眼妆</span>
    <img src="../../assets/img/3断货王/断货王/share.png" alt="">
  </div>
  <div class="bigpic">
    <img src="../../assets/img/1首页/1推荐/lunbo_xunli.png" alt="">
  </div>
  <!--轮播图-->
  <div class="swiper-container wrap">
    <div class="swiper-wrapper">
      <div class="swiper-slide"><span>防晒必备</span></div>
      <div class="swiper-slide"><span>随身面膜</span></div>
      <div class="swiper-slide"><span>分装瓶</span></div>
      <div class="swiper-slide"><span>化妆水</span></div>
      <div class="swiper-slide"><span>化妆水</span></div>
      <div class="swiper-slide"><span>化妆水</span></div>
      <div class="swiper-slide"><span>化妆水</span></div>
      <div class="swiper-slide"><span>化妆水</span></div>
      <div class="swiper-slide"><span>化妆水</span></div>
    </div>
  </div>
  <div class="good" v-for="obj in goods">
    <div><img :src="obj.img" alt=""></div>
    <div>{{obj.name}}</div>
    <div>{{obj.price}}</div>
    <span>立即购买</span>
  </div>
</div>
</template>

<script>
  import * as $ from 'jquery'
  import * as Swiper from 'swiper'
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
  export default {
    data() {
      return {
        goods: []
      }
    },
    methods: {
      doBack() {
        this.$router.go(-1);
      }
    },
    created() {
      axios.post('/goods/geteyes', {
        kind: '绚丽眼妆'
      }).then((data) => {
        this.goods = data.data;
      })
    },
    mounted() {
      new Swiper['default']('.swiper-container', {
        loop: true,
        freeMode : true,
        slidesPerView : 4,
      })
    }
  }
</script>

<style scoped>
  .wrap div{
    text-align: center;
  }
  .body {
    background: white;
    width: 100%;
    overflow: scroll;
    position: absolute;
    top: 0;
    margin-bottom: 4rem;
    z-index: 300;
    overflow: hidden;
  }

  .top {
    width: 100%;
    height: 3rem;
    background: red;
    color: white;
    font-size: 1.5rem;
  }

  .top img {
    height: 1.5rem;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
  }

  .top span {
    margin-left: 8rem;
    margin-right: 5rem;
  }

  .bigpic img {
    width: 100%;
  }
  .good{
    width: 9rem;
    text-align: center;
    border: gainsboro 1px solid;
    padding: 0.5rem;
    float: left;
    margin: 1rem;
  }
  .good img{
    height: 8rem;
  }
  .good div:nth-child(2){
    padding: 0.5rem;
    color: #4d4d4d;
    border-top: 1px solid gainsboro;
  }
  .good div:nth-child(3){
    color: red;
    padding: 0.2rem;
  }
  .good span{
    border-radius: 1rem;
    color: white;
    background: red;
    font-size: 0.7rem;
    padding: 0.3rem;
  }
</style>



