<template>
  <div class="wrapper">
    <div class="header">
      <router-link :to="{path: '/personalinfo'}" class="router"><img src="../../../assets/img/2分类/返回.png" alt="" ></router-link>
      <span>头像</span>
    </div>
    <div class="content">
      <div class="bigImg" @click="operation"><img src="../../../assets/img/5个人中心/1个人中心/头像.png" alt=""></div>
      <div class="mask" v-if="isShow" @click="doCancel"></div>
      <div class="operation" v-if="isShow">
        <p>从手机相册中选择</p>
        <p>拍照</p>
        <p @click="doCancel">取消</p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        isShow: false
      }
    },
    methods: {
      operation() {
        this.isShow = true;
      },
      doCancel() {
        this.isShow = false;
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span:nth-child(2) {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .header>span:nth-child(3) {
    font-size: 1rem;
    color: #ffffff;
    vertical-align: middle;
    position: absolute;
    right: 0.6rem;
    top: 1.2rem;
  }
  .content {
    width: 100%;
    overflow: scroll;
    margin-top: 3rem;
    padding-bottom: 11rem;
  }
  .bigImg {
    margin: 5rem auto;
    width: 16rem;
    height: 16rem;
  }
  .bigImg>img {
    width: 16rem;
    height: 16rem;
  }
  .mask {
    position: absolute;
    background-color: #000000;
    opacity: 0.45;
    top: 0;
    bottom: -11rem;
    left: 0;
    right: 0;
    z-index: 7
  }
  .operation {
    position: fixed;
    bottom: 0;
    z-index: 800;
    background-color: #ffffff;
    width: 100%;
  }
  .operation>p {
    width: 100%;
    text-align: center;
    border-bottom: 1px solid #cccccc;
    padding: 1rem 0;
  }
</style>



