<template>
  <div class="wrapper">
    <div class="header">
      <router-link :to="{path: '/personal'}" class="router"><img src="../../../assets/img/2分类/返回.png" alt="" ></router-link>
      <span>订单详情</span>
      <span @click="edit" v-if="iseditShow">编辑</span>
    </div>
    <div class="content">
      <div class="nav">
        <router-link :to="{path: '/allorder/all'}" class="navrouter"><span @click="editShow">全部订单</span></router-link>
        <router-link :to="{path: '/allorder/pay'}" class="navrouter"> <span @click="editShow">带支付</span></router-link>
        <router-link :to="{path: '/allorder/post'}" class="navrouter"><span @click="editHide">带发货</span></router-link>
        <router-link :to="{path: '/allorder/get'}" class="navrouter"><span @click="editHide">带收货</span></router-link>
        <router-link :to="{path: '/allorder/complete'}" class="navrouter"><span @click="editShow">交易完成</span></router-link>
      </div>
      <div class="goods">
        <router-view></router-view>
      </div>
      <transition name="del">
        <div class="bottom" v-if="duihao">
          <div @click="chooseAll">
            <div class="duihao" :class="{'active': isChoose === true}"></div>
            <span>全选</span>
          </div>
          <span @click="doDel">删除</span>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        isChoose: false,
        duihao: false,
        iseditShow: true
      }
    },
    methods: {
      chooseAll() {
        this.isChoose = !this.isChoose;
        this.$root.showVM.$emit('chooseall', this.isChoose)
      },
      edit() {
        this.duihao = !this.duihao;
        this.$root.showVM.$emit('edit', this.duihao);
      },
      editShow() {
        this.iseditShow = true;
        this.duihao = false;
      },
      editHide() {
        this.iseditShow = false;
        this.duihao = false;
      },
      doDel() {

      }
    },
    created() {
      this.$root.showVM.$on('chooseall1', (v) => {
        this.isChoose = v;
      })
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span:nth-child(2) {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .header>span:nth-child(3) {
    font-size: 1rem;
    color: #ffffff;
    vertical-align: middle;
    position: absolute;
    right: 0.6rem;
    top: 1.2rem;
  }
  .content {
    width: 100%;
    overflow: scroll;
    margin-top: 3rem;
  }
  .nav {
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-bottom: 1px solid #cccccc;
    padding: 0 0.6rem;
  }
  .nav>.navrouter {
    color: black;
    font-size: 1rem;
    padding: 0.5rem 0;
  }
  .nav>.router-link-active {
    color: red;
    border-bottom: 1px solid red;
  }
  .bottom {
    width: 100%;
    height: 1rem;
    padding: 1rem 0.6rem;
    position: fixed;
    background-color: #ffffff;
    bottom: 0;
    z-index: 999;
  }
  .bottom>div {
    position: absolute;
    left: 0.6rem;
    top: 1rem;
    width: 8rem;
  }
  .bottom>div>.duihao {
    background: url("../../../assets/img/4购物车/huiduihao.png") no-repeat center;
    background-size: 100% 100%;
    width: 1rem;
    height: 1rem;
    margin-right: 0.6rem;
    display: inline-block;
    vertical-align: middle;
  }
  .bottom>div>span {
    vertical-align: middle;
  }
  .bottom>span {
    float: right;
    margin-right: 1.25rem;
    background: #e53e42;
    border-radius: 0.6rem;
    width: 5rem;
    color: #ffffff;
    text-align: center;
  }
  .bottom>div>.active {
    background: url("../../../assets/img/4购物车/duihao.png") no-repeat center;
    background-size: 100% 100%;
  }
  /*.del-enter{*/
    /*transform: translateY(0);*/
  /*}*/
  /*.del-leave-to{*/
    /*opacity: 1;*/
    /*transform: translateY(-3rem);*/
  /*}*/
  .del-enter-active{
    animation: moveinto 0.5s linear forwards;
    animation-fill-mode: forwards ;
  }
  .del-leave-active{
    animation: moveinto 0.5s reverse forwards linear;
    animation-fill-mode: forwards;
  }
  @keyframes moveinto {
    0% {
      bottom: -3rem;
      opacity: 0;
    }
    50% {
      bottom: -1.5rem;
    }
    100% {
      bottom: 0rem;
      opacity: 1;
    }
  }
</style>



