<template>
  <div class="wrapper" v-if="show">
    <router-link :to="{path: '/homepage'}" class="router"><div class="homepage"></div><p>首页</p></router-link>
    <router-link :to="{path: '/classify'}" class="router"><div class="classify"></div><p>分类</p></router-link>
    <router-link :to="{path: '/stockout'}" id="big" class="router"><img src="../../assets/img/1首页/1推荐/duanhuowang.png" alt="" ></router-link>
    <router-link :to="{path: '/shopCarts'}" class="router"><div class="shopcarts"></div><p>购物车</p></router-link>
    <router-link :to="{path: '/personal'}" class="router"><div class="personal"></div><p>个人中心</p></router-link>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        show: 1
      }
    },
    created() {
      this.$root.showVM.$on('isshow', (v) => {
        this.show = v;
      })
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 3rem;
    position: fixed;
    bottom: 0;
    padding: 0.4rem 1.2rem 0 0.7rem;
    box-shadow: 0rem 0rem 0rem 0rem #e6e6e6;
    background: #ffffff;
    border-top: 1px solid #cccccc;
    z-index: 200;
  }
  .router {
    display: inline-block;
    width: 3rem;
    text-align: center;
    margin-right: 1rem;
  }
  .router>div {
    width: 1.5rem;
    height: 1.5rem;
    margin: 0 auto;
    background-size: 100% 100%;
  }
  .homepage {
    background: url("../../assets/img/1首页/1推荐/shouyeqian.png") center no-repeat;
  }
  .classify {
    background: url("../../assets/img/1首页/1推荐/feilei.png") center no-repeat;
  }
  .shopcarts {
    background: url("../../assets/img/1首页/1推荐/gouwucheqian.png") center no-repeat;
  }
  .personal {
    background: url("../../assets/img/1首页/1推荐/gerenqian.png") center no-repeat;
  }
  #big>img{
    width: 3.5rem;
    height: 1.7rem;
    margin-bottom: 2rem;
    vertical-align: middle;
  }
  p {
    width: 3rem;
    height: 0.5rem;
    font-family: MicrosoftYaHei;
    font-size: 0.6rem;
    line-height: 1.1rem;
    color: #666666;
  }
  .router-link-active>.homepage {
    background: url("../../assets/img/1首页/1推荐/shouyehou.png") center no-repeat;
    background-size: 100% 100%;
  }
  .router-link-active>.classify {
    background: url("../../assets/img/1首页/1推荐/fenleihou.png") center no-repeat;
    background-size: 100% 100%;
  }
  .router-link-active>.shopcarts {
    background: url("../../assets/img/1首页/1推荐/gouwuchehou.png") center no-repeat;
    background-size: 100% 100%;
  }
  .router-link-active>.personal {
    background: url("../../assets/img/1首页/1推荐/gerenhou.png") center no-repeat;
    background-size: 100% 100%;
  }
</style>



