<template>
  <div class="body">
    <!--页眉-->
    <div class="top">
      <img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack">
      <span>品牌精选</span>
      <img src="../../assets/img/3断货王/断货王/share.png" alt="">
    </div>
    <div class="bigpic">
      <img src="../../assets/img/1首页/qingdan/datuHABA.png" alt="">
    </div>
    <div class="good">
      <div><img :src="goods.img" alt=""></div>
      <div>{{goods.name}}</div>
      <div>{{goods.price}}</div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);

    export default {
       data() {
         return{
           goods:''
         }
       },
      methods:{
        doBack() {
          this.$router.go(-1);
        }
      },
      created() {
        axios.post('/goods/getBrand', {
          kind: '品牌精选'
        }).then((data) => {
          this.goods = data.data[0];
          console.log(this.goods);
        })
      }
    }
</script>

<style scoped>
  .body {
    background: white;
    width: 100%;
    overflow: scroll;
    position: absolute;
    top: 0;
    margin-bottom: 4rem;
    z-index: 300;
  }

  .top {
    width: 100%;
    height: 3rem;
    background: red;
    color: white;
    font-size: 1.5rem;
  }

  .top img {
    height: 1.5rem;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
  }

  .top span {
    margin-left: 3rem;
    margin-right: 1rem;
  }

  .bigpic img {
    width: 100%;
  }
  .good{
    width: 12rem;
    text-align: center;
    border: gainsboro 1px solid;
    padding: 0.5rem;
  }
  .good img{
    height: 8rem;
  }
  .good div:nth-child(2){
    padding: 0.5rem;
    color: #4d4d4d;
    border-top: 1px solid gainsboro;
  }
  .good div:nth-child(3){
    color: red;
    padding: 0.2rem;
  }
</style>



