<template>
  <div class="body">
    <!--页眉-->
    <div class="top">
      <img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack">
      <span>健康美味兼得的布尔本</span>
      <img src="../../assets/img/3断货王/断货王/share.png" alt="">
    </div>
    <div class="bigpic">
      <img src="../../assets/img/1首页/超级热卖/jiankangbuerben.png" alt="">
    </div>
    <div class="brandtxt">
      日本著名糕点品牌Bourbon布尔本，成立于1924年，
      是日本零食十强之一。制作的曲奇和糕点系列，品质
      远胜于欧洲传统西点，做工研究、精致、高贵、时尚、
      健康是Bourbon一贯的严谨作风，其北日本系列礼盒
      更是夺得了销售冠军，相当受欢迎。
    </div>
    <div class="xilie">
      <img src="../../assets/img/1首页/超级热卖/quqibinggan.png" alt="">
    </div>
    <div class="good">
        <div><img :src="goods[0].img" alt=""></div>
        <div>{{goods[0].name}}</div>
        <div>{{goods[0].price}}</div>
        <span>立即购买</span>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
  export default {
    data() {
      return {
        goods: []
      }
    },
    methods: {
      doBack() {
        this.$router.go(-1);
      }
    },
    created() {
      axios.post('/goods/getHots', {
        kind: '超值热卖'
      }).then((data) => {
        this.goods = data.data;
        console.log(this.goods);
      })
    }
  }
</script>

<style scoped>
  .body {
    background: white;
    width: 100%;
    overflow: scroll;
    position: absolute;
    top: 0;
    margin-bottom: 4rem;
    z-index: 300;
  }

  .top {
    width: 100%;
    height: 3rem;
    background: red;
    color: white;
    font-size: 1.5rem;
  }

  .top img {
    height: 1.5rem;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
  }

  .top span {
    margin-left: 3rem;
    margin-right: 1rem;
  }

  .bigpic img {
    width: 100%;
  }

  .brandtxt {
    width: 92%;
    height: 8rem;
    background-color: #ffcd2c;
    padding: 1rem;
  }

  .xilie img {
    width: 100%;
  }
  .good{
    width: 12rem;
    text-align: center;
    border: gainsboro 1px solid;
    padding: 0.5rem;
  }
  .good img{
    height: 8rem;
  }
  .good div:nth-child(2){
    padding: 0.5rem;
    color: #4d4d4d;
    border-top: 1px solid gainsboro;
  }
  .good div:nth-child(3){
    color: red;
    padding: 0.2rem;
  }
  .good span{
    border-radius: 1rem;
    color: white;
    background: red;
    font-size: 0.7rem;
    padding: 0.3rem;
  }
</style>



