<template>
    <div class="body">
      <!--页眉-->
      <div class="top">
        <img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack">
        <span>本周新品</span>
        <img src="../../assets/img/3断货王/断货王/share.png" alt="">
      </div>
      <div class="title">
        <h2>本周新品</h2>
        <h3>精选环球好物</h3>
      </div>
      <div class="search" >
        <input type="text" placeholder="请输入商品关键字">
      </div>
      <div v-for="">
        <div>
          <img src="../../assets/img/1首页/4母婴/haibao_mamibaobei.png" alt="" class="bg">
        </div>
        <div class="three">
          <ul class="flex">
            <li><img src="../../assets/img/1首页/4母婴/naifen.png" alt=""></li>
            <li class="flex2"><img src="../../assets/img/1首页/4母婴/naifen.png" alt=""></li>
            <li><img src="../../assets/img/1首页/4母婴/naifen.png" alt=""></li>
          </ul>
        </div>
        <div class="three">
          <ul class="flex" style="color: red">
            <li>
              <div>圣元奶粉</div>
              <div>¥666</div>
            </li>
            <li>
              <div>圣元奶粉</div>
              <div>¥666</div>
            </li>
            <li>
              <div>圣元奶粉</div>
              <div>¥666</div>
            </li>
          </ul>
        </div>
      </div>

    </div>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
    export default {
      data() {
        return{
          goods:[]
        }
      },
       methods:{
         doBack() {
           this.$router.go(-1);
         }
       },
      created() {
        axios.post('/goods/getnews', {
          kind:'本周新品'
        }).then((data) => {
          this.goods = data.data;
          console.log(this.goods);
        })
      }
    }
</script>

<style scoped>
  .body {
    background: white;
    width: 100%;
    overflow: scroll;
    position: absolute;
    top: 0;
    margin-bottom: 4rem;
    z-index: 300;
  }
  .top {
    width: 100%;
    height: 3rem;
    background: red;
    color: white;
    font-size: 1.5rem;
  }

  .top img {
    height: 1.5rem;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
  }

  .top span {
    margin-left: 7rem;
    margin-right: 2.5rem;
  }
  .title{
    padding: 0.5rem;
  }
  .title h2{
    color: red;
  }
  .title h3{
    color: grey;
  }
  .search{
    width: 100%;
    height: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    background: gainsboro;
    text-align: center;
  }
  input::-webkit-input-placeholder{
    color: #e83d41;;opacity:1;
  }
  .search input{
    width: 21rem;
    height: 2rem;
    background-color: #ffffff;
    border-radius: 0rem;
    font-family: MicrosoftYaHei;
    font-size: 1rem;
    font-weight: normal;
    font-stretch: normal;
    line-height: 1rem;
    letter-spacing: 0rem;
    text-align: center;
    color: #e83d41;
  }
  .bg {
    width: 100%;
  }

  .flex {
    display: flex;
  }

  .flex li {
    flex: 1;
    text-align: center;
  }
  .flex2{
    border-right: 1px solid gainsboro;
    border-left: 1px solid gainsboro;
  }

  .flex li > img {
    height: 5rem;
  }

</style>



