<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
  export default {
    data() {
      return {
        isLogin: false,
        nickname: '',
        isShow: false,
        isShow2: false,
        iscover: false,
        numisShow: false,
        passisShow: false,
        fastIsShow: false,
        loseisShow: false,
        numb: '',
        pass: '',
        error: ''
      }
    },
    methods: {
      sendToFooter() {
        this.$root.showVM.$emit('isshow', this.show1);
      },
      toAllOrder() {
        this.$root.showVM.$emit('isshow', this.show2);
        this.$router.push({name: 'allorder'});
      },
      toPay() {
        this.$root.showVM.$emit('isshow', this.show2);
      },
      toPost() {
        this.$root.showVM.$emit('isshow', this.show2);
      },
      toGet() {
        this.$root.showVM.$emit('isshow', this.show2);
      },
      toComplete() {
        this.$root.showVM.$emit('isshow', this.show2);
      },
      doLogin1() {
        axios.post('/goods/login', {
          ph:this.numb,
          pw:this.pass
        }).then((data) => {
          console.log(data)
          this.isLogin = true;
          this.isShow = false;
          this.isShow2 = false;
          this.nickname = data.data.pn
          console.log(this.nickname)
        })
      },
      doClick(e) {
        if (e.target.innerHTML === '登录/注册') {
          // 注册登录
          this.isShow = true;
        }
      },
      toRegister() {
        this.$router.push({name: 'register'})
        this.$root.showVM.$emit('isshow', this.show1);
      },
      toInfo() {
        this.$router.push({name: 'personalinfo'})
        this.$root.showVM.$emit('isshow', this.show1);
      },
      doLogin() {
        this.isShow2 = true;
        this.iscover = true;
      },
      delNum() {
        this.numb = ''
      },
      delPass() {
        this.pass = ''
      },
      doCancel() {
        this.isShow = false;
        this.isShow2 = false;
        this.iscover = false;
        this.fastIsShow = false;
        this.loseisShow = false;
      },
      toFastLogin() {
        this.fastIsShow = true;
        this.isShow2 = false;
        this.loseisShow = false;
      },
      toPassLogin() {
        this.fastIsShow = false;
        this.isShow2 = true;
        this.loseisShow = false;
      },
      toLosePass() {
        this.loseisShow = true;
        this.fastIsShow = false;
        this.isShow2 = false;
      }
    },
    created() {
      if (this.isLogin === false) {
        this.nickname = '登录/注册';
      }
      if (this.numb !== '') {
        this.numisShow = true;
      }
    },
    watch: {
      numb: function (v1, v2) {
        if (v1 === '') {
          this.numisShow = false;
        } else {
          this.numisShow = true;
        }
      },
      pass: function (v1, v2) {
        if (v1 === '') {
          this.passisShow = false;
        } else {
          this.passisShow = true;
        }
      }
    }
  }
</script>
