<template>
  <div class="body">
    <div class="wrapper">
      <ul class="kind">
        <li  @click="doClick(reckind)" :class="{'active': activeName === reckind}" :style="rec1"><router-link :to="{path: '/classify/recommend'}" class="router" :style="rec2">{{reckind}}</router-link></li>
        <li v-for="kind in kinds" @click="doClick(kind)" :class="{'active': activeName === kind}">
          <router-link :to="{path: '/classify/kinds'}" class="router">{{kind}}</router-link>
        </li>
      </ul>
    </div>
    <div class="content">
      <router-view :fromparent="tochild"></router-view>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'
  export default {
    data() {
      return {
        reckind: '为您推荐',
        kinds: [],
        tochild: '',
        activeName: '',
        rec1: [
          {
            borderRight: "2px solid red"
          }
        ],
        rec2: [
          {
            color: "red"
          }
        ]
      }
    },
    methods: {
      doClick(kind) {
        this.activeName = kind;
        this.rec1 = null;
        this.rec2 = null;
        this.tochild = kind;
      }
    },
    created() {
      Vue.axios.get('/goods/getall').then((data)=> {
        let kinds1 = [];
        for (let i = 0; i < data.data.length; i++) {
          kinds1.push(data.data[i].kind1);
        }
        for(var i = 0; i < kinds1.length;i++){
          if(this.kinds.indexOf(kinds1[i]) === -1){
            this.kinds.push(kinds1[i]);
          }
        }
      })
    }
  }
</script>
<style scoped>
  .body {
    width: 100%;
    margin-top: 3rem;
  }
  .wrapper {
    position: relative;
    width: 25%;
  }
  .content {
    float: right;
    width: 74%;
  }
  .kind {
    width: 5.6rem;
    position: absolute;
    top: 0;
    left: 0;
    background: #ffffff;
  }
  li {
    width: 5.6rem;
    height: 2.5rem;
    text-align: center;
    font-family: MicrosoftYaHei;
    font-size: 0.9rem;
    line-height: 2.8rem;
    border: 1px solid #cccccc;
    position: relative;
  }
  .router {
    color: #4d4d4d;
  }
  .active {
    border-right: 2px solid red;
  }
  .active>.router {
    color: red;
  }
</style>



