<template>
  <div class="wrapper">
    <div class="header">
      <router-link :to="{path: '/personal'}" class="router"><img src="../../../assets/img/2分类/返回.png" alt="" ></router-link>
      <span>设置</span>
    </div>
    <div class="content">
      <div @click="toAccount"><span>账户设置</span><span> > </span></div>
      <div @click="toPasswords"><span>修改登录密码</span><span> > </span></div>
      <div><span @click="clearCache">清除缓存</span><span> {{cache}}KB </span></div>
      <div><span>阿拉灯平台用户协议</span><span> > </span></div>
      <div><span>个人委托协议</span><span> > </span></div>
      <div @click="doCheck"><span>检查更新</span><span> > </span></div>
    </div>
    <div class="checkDiv">
      <div class="check" v-if="isShow">检查中...</div>
      <div class="check2" v-if="isShow2">已是最新版本</div>
      <div class="clearComplete" v-if="isShow4">清除成功</div>
    </div>
    <div class="mask" v-show="isShow3"></div>
    <div class="cache" v-if="isShow3">
      <div>确认清楚缓存?</div>
      <div class="operation"><span @click="clearCancel">取消</span><span style="color: red" @click="clearSure">确认</span></div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        isShow: false,
        isShow2: false,
        isShow3: false,
        isShow4: false,
        cache: 100
      }
    },
    methods: {
      toAccount() {
        this.$router.push({name: 'accountoption'})
      },
      toPasswords() {
        this.$router.push({name: 'passwords'})
      },
      doCheck() {
        this.isShow = true;
        setTimeout(() => {
          this.isShow = false;
          this.isShow2 = true;
          setTimeout(() => {
            this.isShow2 = false;
          }, 1500);
        }, 2000);
      },
      clearCache() {
        this.isShow3 = true;
      },
      clearSure() {
        this.isShow3 = false;
        this.cache = 0;
        this.isShow4 = true;
        setTimeout(() => {
          this.isShow4 = false;
        }, 1500)
      },
      clearCancel() {
        this.isShow3 = false;
      }
    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    position: relative;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span:nth-child(2) {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .header>span:nth-child(3) {
    font-size: 1rem;
    color: #ffffff;
    vertical-align: middle;
    position: absolute;
    right: 0.6rem;
    top: 1.2rem;
  }
  .content {
    width: 100%;
    overflow: scroll;
    margin-top: 3rem;
  }
  .content>div {
    padding: 0.6rem 0 0.6rem 0.6rem;
    background-color: #ffffff;
    overflow: hidden;
  }
  .content>div:nth-child(1) {
    border-bottom: 1px solid #cccccc;
  }
  .content>div>span {
    color: #4d4d4d;
  }
  .content>div>span:nth-child(2) {
    float: right;
    margin-right: 1rem;
  }
  .checkDiv {
    position: relative;
    width: 100%;
    margin-top: 2rem;
  }
  .check {
    width: 4rem;
    background-color: #000000;
    color: #ffffff;
    padding: 0.5rem 1.5rem;
    position: absolute;
    top: 0;
    left: 7rem;
  }
  .check2 {
    background-color: #000000;
    color: #ffffff;
    padding: 0.5rem 1.5rem;
    position: absolute;
    top: 0;
    left: 7rem;
    z-index: 200;
  }
  .clearComplete {
    background-color: #000000;
    color: #ffffff;
    padding: 0.5rem 1.5rem;
    position: absolute;
    top: 0;
    left: 7rem;
    z-index: 200;
  }
  .mask {
    position: absolute;
    background-color: #000000;
    opacity: 0.45;
    top: 0;
    bottom: -20rem;
    left: 0;
    right: 0;
  }
  .cache {
    background-color: #ffffff;
    position: absolute;
    z-index: 200;
    border: 1px solid #cccccc;
    top: 12rem;
    left: 3.7rem;
  }
  .cache>div:nth-child(1){
    padding: 2.25rem 4rem;
  }
  .operation>span{
    display: inline-block;
    width: 49.1%;
    border: 1px solid #cccccc;
    padding: 1rem 0;
    text-align: center;
  }
</style>



