<template class="temp">
  <div class="body">
    <!--心得-->
    <div class="xinde" v-for="obj in comment">
      <div @click="toEvaluatedetails">
      <div class="persen">
        <img :src="obj.pic" alt="">
        <span class="nic">{{obj.nickname}}</span>
        <span>{{obj.time}}</span>
      </div>
      <div class="txt">
        {{obj.txt}}
      </div>
      <div class="pic">
        <img :src="obj.goodimg" alt="">
      </div>
      </div>
      <div class="fenlan">
        <ul class="flex">
          <li><img src="../../assets/img/3断货王/全部心得/zan.png" alt="">{{obj.nice}}</li>
          <li><img src="../../assets/img/3断货王/全部心得/comment.png" alt="">{{obj.comment.length}}</li>
          <li><img src="../../assets/img/3断货王/全部心得/share.png" alt="">26</li>
        </ul>
      </div>
      <!--条-->
      <div class="tiao"></div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
  Vue.config.productionTip = false;

  export default {
    data() {
      return{
        comment:Array
      }
    },
    methods: {
      toEvaluatedetails() {
        this.$router.push({name: 'evaluate/evaluatedetails', params: {comment: this.comment}});
      }
    },
    created() {
      this.comment = [];
      axios.post('/goods/getcomments', {
        goodname:'娇吻玉口红'
      }).then((data) => {
        this.comment.push(data.data);
        console.log(this.comment);
      })
    }
  }
</script>

<style scoped>
  .body{
    width: 100%;
    margin-top: 6rem;
    overflow: scroll;
  }
  .tiao{
    width: 100%;
    height: 1.5rem;
    background: gainsboro;
    text-align: center;
    opacity: 0.8;
  }
  .temp{
    width: 100%;
    background: white;
    overflow: scroll;
    margin-top: 3rem;
  }
  .xinde {

  }
  .persen{
    padding: 1rem;
  }
  .nic{
    margin-right: 11rem;
  }
  .persen img {
    vertical-align: middle;
    margin-right: 0.5rem;
  }
  .txt{
    padding-left: 1rem;
    padding-right: 3rem;
    color: #808080;
  }
  .pic{
    border-bottom: 2px solid gainsboro;
    padding-bottom: 1rem;
  }
  .pic img{
    height: 6rem;
    margin-left: 1rem;
    margin-top: 0.5rem;
    border: 1px solid gainsboro;
  }
  .flex{
    display: flex;
    padding: 0.5rem;
  }
  .flex img{
    height: 1rem;
    vertical-align: middle;
  }
  .flex li{
    flex: 1;
    text-align: center;
    color: #808080;
  }
  .flex li:nth-child(2){
    border-right: 2px solid gainsboro;
    border-left: 2px solid gainsboro;
  }
</style>



