        axios.post('/goods/getsum', {
          name: e.name
        }).then( (data) => {
          if (data.data.err == null) {
            axios.post('/goods/sendsCar',  {
              name: e.name,
              spec: '无',
              num: 1,
              price:e.price,
              img: e.img
            }).then( () => {});
          } else {
            const  sum = data.data.err.num + 1;
            axios.post('/goods/sendsCar',  {
              name: e.name,
              num: sum,
            }).then( (data2) => {
            });
