<template>
  <div class="body">
    <!--断货王-->
    <div class="tuijian">
      <div>
        <img src="../../assets/img/1首页/1推荐/lunbo_xunli.png" alt="" class="bg">
      </div>
      <div class="three" >
        <ul class="flex" >
          <li v-for="obj in goods"><img :src="obj.img" alt=""></li>
        </ul>
      </div>
      <div class="three" >
        <ul class="flex" style="color: red"  >
          <li v-for="obj1 in goods">
            <div>{{obj1.name}}</div>
            <div>{{obj1.price}}</div>
            <div class="num">还剩1000件</div>
            <div class="qiang" @click="toProduct(obj1)">马上去抢</div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);
  export default {
    data() {
      return{
        goods:[],
        name:''
      }
    },
     methods:{
       toProduct(e) {
         this.name = e;
         this.$router.push({name: 'product', params: {name: JSON.stringify(this.name)}});
       }
     },
    created() {
      axios.post('/goods/geteyes', {
        kind: '绚丽眼妆'
      }).then((data) => {
        this.goods = data.data;
      })
    }
  }
</script>
<style scoped>
  .body{
    width: 100%;
    margin-top: 3rem;
    overflow: scroll;
    margin-bottom: 4rem;
  }

  .tuijian {
    width: 100%;
    margin-top: 2rem;
  }

  .bg {
    width: 100%;
  }

  .flex {
    display: flex;
  }

  .flex li {
    flex: 1;
    text-align: center;
  }

  .flex li > img {
    height: 5rem;
  }
  .num{
    font-size: 0.8rem;
    width: 80%;
    margin: auto;
    color: black;
    border-top:1px solid gainsboro;
  }
  .qiang{
    width: 65%;
    border-radius: 1rem;
    background: red;
    color: white;
    margin: auto;
  }
</style>



