<template>
  <div class="wrapper">
    <div class="header">
      <router-link :to="{path: '/address'}" class="router"><img src="../../assets/img/2分类/返回.png" alt="" ></router-link>
      <span>添加新地址</span>
      <router-link :to="{name: 'address', params: {realname: realname, phoneNumber: phoneNumber,city: city, address: address, postcode: postcode}}" class="router1">完成</router-link>
    </div>
    <div class="content">
      <div class="warning">
        请使用您的真实姓名,否则订单无法发出
      </div>
      <div class="name">
        <input type="text" placeholder="请填写真实姓名" v-model.lazy.trim="realname">
      </div>
      <div class="number">
        <input type="text" placeholder="请填写手机号" v-model.lazy.trim.number="phoneNumber">
      </div>
      <div class="city">
        <input type="text" placeholder="请选择城市" v-model.lazy.trim="city">
      </div>
      <div class="adrress">
        <input type="text" placeholder="请填写详细地址" v-model.lazy.trim="address">
      </div>
      <div class="postcode">
        <input type="text" placeholder="请填写邮编" v-model.lazy.trim="postcode">
      </div>
    </div>
    <div class="bg"></div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        realname: '',
        phoneNumber: '',
        city: '',
        address: '',
        postcode: ''
      }
    },
    methods: {

    }
  }
</script>
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    position: relative;
    background-color: #f2f2f2;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    background: #e53e42;
    position: fixed;
    padding: 0.75rem 0;
    top: 0;
    z-index: 400;
    text-align: center;
  }
  .router {
    margin-left: 0.6rem;
  }
  .router>img {
    width: 1rem;
    height: 1.5rem;
    position: absolute;
    left: 0.75rem;
    top: 1rem;
  }
  .header>span {
    font-size: 1.5rem;
    color: #ffffff;
    vertical-align: middle;
  }
  .router1 {
    color: #ffffff;
    position: absolute;
    font-size: 1rem;
    right: 0.3rem;
    top: 1rem;
  }
  .content {
    width: 100%;
    margin-top: 3.2rem;
    overflow: scroll;
    overflow-x: hidden
  }
  .content>div{
    width: 100%;
  }
  .warning {
    background-color: #f2f2f2;
    color: red;
    font-size: 0.75rem;
    padding: 0.3rem 0.6rem;
  }
  .content>div {
    border-bottom: 1px solid #f2f2f2;
  }
  .content>.name {
    margin-bottom: 1rem;
  }
  .content>div>input {
    width: 100%;
    padding: 0.6rem;
    appearance: none;
    border: none;
    outline: none;
  }
</style>



