<template>
  <div class="body">
    <!--页眉-->
    <div class="top">
      <img src="../../assets/img/3断货王/断货王/back.png" alt="" @click="doBack">
      <span>限时断货</span>
      <img src="../../assets/img/3断货王/断货王/share.png" alt="">
    </div>
    <div class="title">
      宝贝即将售罄,再不抢就没了.
    </div>
    <div class="goods" v-for="obj in goods">
      <div class="pic"><img :src="obj.img" alt=""></div>
      <div class="txt">
        <h3>{{obj.name}}</h3>
        <span class="span">{{obj.price}}</span><span class="go">马上去抢 >></span>
      </div>
    </div>
    <div class="timeout">
      <TimeOut endTime="1524861620" :callback="callback" endText="已经结束了"></TimeOut>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'
  import TimeOut from '../HomePage/TimeOut'

  Vue.use(VueAxios, axios);
    export default {
      data() {
        return{
          goods:[]
        }
      },
      methods:{
        doBack() {
          this.$router.go(-1);
        },
        callback(){
          if(this.callback && this.callback instanceof Function){
            this.callback(...this);
          }
        }
      },
      components:{
        TimeOut
      },
      created() {
        axios.post('/goods/gettimeoutgoods', {
          kind:'限时断货'
        }).then((data) => {
          this.goods = data.data;
          console.log(this.goods);
        })
      }
    }
</script>

<style scoped>
  .body {
    background: white;
    width: 100%;
    overflow: scroll;
    position: absolute;
    top: 0;
    margin-bottom: 4rem;
    z-index: 300;
  }
  .top {
    width: 100%;
    height: 3rem;
    background: red;
    color: white;
    font-size: 1.5rem;
  }

  .top img {
    height: 1.5rem;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
  }

  .top span {
    margin-left: 7rem;
    margin-right: 2.5rem;
  }
  .title{
    width: 100%;
    height: 2rem;
    text-align: center;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-size: 1.4rem;
    background: gainsboro;
    color: grey;
  }
  .goods{
    padding: 0.5rem;
    overflow: hidden;
  }
  .goods div{
    float: left;
  }
  .pic img{
    height: 6rem;
  }
  .pic{
    margin-right: 1rem;
  }
  .go{
    width: 5rem;
    border-radius: 2rem ;
    font-size: 0.8rem;
    background: red;
    color: white;
    padding: 0.5rem;
  }
  .span{
    color: red;
    display: inline-block;
    margin-top: 2rem;
    margin-right: 6rem;
  }
  .timeout{
    margin-left: 16rem;
    font-size: 0.8rem;
  }
</style>



